import { Component } from '@angular/core';
import { OnInit } from '@angular/core';
import { ConsigneeService } from "../../../services/party/consignee.service";
import { BaseLookupDialogComponent } from "./base-lookup-dialog.component";

@Component({
    selector: 'consignee-lookup-dialog',
    template: require('./consignee-lookup-dialog.component.html'),
    providers: [
        ConsigneeService
    ]
})
export class ConsigneeLookupDialogComponent extends BaseLookupDialogComponent implements OnInit {

    constructor(private consigneeService: ConsigneeService) {
        super();
    }

    ngOnInit() {
        this.consigneeService.getAll().subscribe((consignees) => {
            this.tableData = consignees;            
        });
    }

    protected doFilter(filter: string) {
        this.consigneeService.getByFilter(filter).subscribe((consignees) => {
            this.tableData = consignees;
            
            if (this.filterQueue.length > 0) {
                this.doFilter(this.filterQueue.pop());
            }
        });
    }
}



