import { DomainObject } from './DomainObject';

export class CargoTypeAware extends DomainObject {
    static CARGO_TYPES: Array<{[key: string]: string}> = [
        { value: "Container", name: "Container" }, 
        { value: "Bulk", name: "Bulk" }, 
        { value: "BreakBulk", name: "BreakBulk" }, 
        { value: "LiquidBulk", name: "LiquidBulk" }, 
        { value: "Vehicle", name: "Vehicle" }, 
        { value: "Multiple", name: "Multiple" }, 
        { value: "Other", name: "Other" }
    ];
    
    public cargoType: "Container" | "Bulk" | "BreakBulk" |
                        "LiquidBulk" | "Vehicle" | "Multiple" | "Other";
                        
    constructor(data?: any) {
        super(data);
        
        if (data) {
            this.cargoType = data.cargoType;
        }
    }
    
    public prepareData(): void {}
}


