import {Injectable, Inject} from '@angular/core';
import {AppProperties} from '../../../app.properties';
import {Http, Response, URLSearchParams} from '@angular/http';
import {Shipment} from '../../../models/Shipment';
import {Container} from '../../../models/Container';
import {Observable} from 'rxjs/Rx';
import {AbstractService} from '../../abstract-service';
import {CacheService} from 'ng2-cache';

/**
 * Shipment service
 * 
 * @export
 * @class ShipmentService
 * @extends {AbstractService}
 */
@Injectable()
export class ShipmentService extends AbstractService {

    /**
     * Creates an instance of ShipmentService.
     * @param {Http} http 
     * @param {AppProperties} appProps 
     * 
     * @memberOf ShipmentService
     */
    constructor(private http: Http,
        @Inject(CacheService)
        private cacheService: CacheService,
        @Inject(AppProperties) appProps: AppProperties) {
        super(appProps);
    }

    /**
     * Returns a list of shipments
     * 
     * @returns {Observable<Array<Shipment>>} 
     * 
     * @memberOf ShipmentService
     */
    public getAll(): Observable<Array<Shipment>> {
        return this.http.get(this.apiBaseUrl + '/shipments', this.getStandardRequestOptions())
            .map(this.extractMany)
            .catch(this.handleError);
    }

    /**
     * Returns a list of shipments filtered by vessel call
     * 
     * @param {number} uid 
     * @returns {Observable<Array<Shipment>>} 
     * 
     * @memberOf ShipmentService
     */
    public getByVesselCall(uid: number): Observable<Array<Shipment>> {
        return this.http.get(this.apiBaseUrl + '/vessel-call/' + uid + '/shipments', this.getStandardRequestOptions())
            .map(this.extractMany)
            .catch(this.handleError);
    }

    /**
     * Returns a list of shipments filtered by master shipment
     * 
     * @returns {Observable<Array<Shipment>>} 
     * 
     * @memberOf ShipmentService
     */
    public getAllMasterShipments(): Observable<Array<Shipment>> {
        let options = this.getStandardRequestOptions();
        let searchParams = new URLSearchParams();

        searchParams.set("shipmentType", "Master");
        options.search = searchParams;

        return this.http.get(this.apiBaseUrl + '/shipments', options)
            .map(this.extractMany)
            .catch(this.handleError);
    }

    /**
     * Returns a list of house shipments
     * 
     * @returns {Observable<Array<Shipment>>} 
     * 
     * @memberOf ShipmentService
     */
    public getAllHouseShipments(): Observable<Array<Shipment>> {
        let options = this.getStandardRequestOptions();
        let searchParams = new URLSearchParams();

        searchParams.set("shipmentType", "House");
        options.search = searchParams;

        return this.http.get(this.apiBaseUrl + '/shipments', options)
            .map(this.extractMany)
            .catch(this.handleError);
    }

    /**
     * Returns a filtered list of shipments
     * 
     * @param {string} filter 
     * @returns {Observable<Array<Shipment>>} 
     * 
     * @memberOf ShipmentService
     */
    public getByFilter(filter: string): Observable<Array<Shipment>> {
        let options = this.getStandardRequestOptions();
        let searchParams = new URLSearchParams();

        searchParams.set("filter", filter);
        options.search = searchParams;

        return this.http.get(this.apiBaseUrl + '/shipments', options)
            .map(this.extractMany)
            .catch(this.handleError);
    }

    /**
     * Returns a filtered list of shipments
     * 
     * @param {string} filter 
     * @returns {Observable<Array<Shipment>>} 
     * 
     * @memberOf ShipmentService
     */
    public getByShipmentNumber(shipmentNumber: string): Observable<Array<Shipment>> {
        let options = this.getStandardRequestOptions();
        let searchParams = new URLSearchParams();

        searchParams.set("shipmentNumber", shipmentNumber);
        options.search = searchParams;

        return this.http.get(this.apiBaseUrl + '/shipments', options)
            .map(this.extractMany)
            .catch(this.handleError);
    }

    /**
     * Returns a shipment identified by uid
     * 
     * @param {number} uid 
     * @returns {Observable<Shipment>} 
     * 
     * @memberOf ShipmentService
     */
    public getById(uid: number): Observable<Shipment> {
        return this.http.get(this.apiBaseUrl + '/shipment/' + uid, this.getStandardRequestOptions())
            .map(this.extractOne)
            .catch(this.handleError);
    }

    /**
     * Returns a shipment identified by uid
     * 
     * @param {number} uid 
     * @returns {Observable<Shipment>} 
     * 
     * @memberOf ShipmentService
     */
    public getShipmentContainers(uid: number): Observable<Array<Container>> {
        return this.http.get(this.apiBaseUrl + '/shipment/' + uid + '/containers',
            this.getStandardRequestOptions())
            .map((res: Response): Array<Container> => {
                let result: Array<Container> = [];
                let jsonRes = res.json();

                if (jsonRes) {
                    jsonRes.forEach((container: any) => {
                        result.push(new Container(container));
                    });
                }

                return result;
            }).catch(this.handleError);
    }

    /**
     * Creates a new shipment
     * 
     * @param {Shipment} shipment 
     * @returns {Observable<Shipment>} 
     * 
     * @memberOf ShipmentService
     */
    public create(shipment: Shipment): Observable<Shipment> {
        this.cacheService.set("vesselCallId", shipment.vesselCallId, null);
        this.cacheService.set("shippingLineId", shipment.shippingLineId, null);

        return this.http.post(this.apiBaseUrl + '/shipments', shipment,
            this.getStandardRequestOptions())
            .map(this.extractOne)
            .catch(this.handleError);
    }

    /**
     * Updates a shipment
     * 
     * @param {number} uid 
     * @param {Shipment} shipment 
     * @returns {Observable<Shipment>} 
     * 
     * @memberOf ShipmentService
     */
    public update(uid: number, shipment: Shipment): Observable<Shipment> {
        return this.http.put(this.apiBaseUrl + '/shipment/' + uid, shipment,
            this.getStandardRequestOptions())
            .map(this.extractOne)
            .catch(this.handleError);
    }

    /**
     * Deletes a shipment
     * 
     * @param {number} uid 
     * @returns {Observable<boolean>} 
     * 
     * @memberOf ShipmentService
     */
    public delete(uid: number): Observable<boolean> {
        return this.http.delete(this.apiBaseUrl + '/shipment/' + uid,
            this.getStandardRequestOptions())
            .map((res: Response) => {
                return true;
            })
            .catch(this.handleError);
    }

    public duplicateShipment(vesselCallUid: number, shipmentUid: number, shipmentNumber: string): Observable<Shipment> {
        return this.http.put(this.apiBaseUrl + '/shipment/' + shipmentUid + "/action", {action: "duplicate", vesselCallUid: vesselCallUid, shipmentNumber: shipmentNumber},
            this.getStandardRequestOptions())
            .catch(this.handleProcessError);
    }

    public transferShipment(vesselCallUid: number, shipmentUid: number): Observable<Shipment> {
        return this.http.put(this.apiBaseUrl + '/shipment/' + shipmentUid + "/action", {action: "transfer", vesselCallUid: vesselCallUid},
            this.getStandardRequestOptions())
            .catch(this.handleProcessError);
    }

    public getByVesselCallExport(uid: number): Observable<Array<Shipment>> {
        return this.http.get(this.apiBaseUrl + '/vessel-call/' + uid + '/manifest/export', this.getStandardRequestOptions())
            .map(this.extractMany)
            .catch(this.handleProcessError);
    }

    public getByVesselCallImport(uid: number): Observable<Array<Shipment>> {
        return this.http.get(this.apiBaseUrl + '/vessel-call/' + uid + '/manifest/import', this.getStandardRequestOptions())
            .map(this.extractMany)
            .catch(this.handleProcessError);
    }

    public getFilterByMasterShipmentExport(vesselUid: number, filter: string): Observable<Array<Shipment>> {
        return this.http.get(this.apiBaseUrl + '/vessel-call/' + vesselUid + '/manifest/export?masterShipmentNumber=' + filter, this.getStandardRequestOptions())
            .map(this.extractMany)
            .catch(this.handleProcessError);
    }

    public getFilterByMasterShipmentImport(vesselUid: number, filter: string): Observable<Array<Shipment>> {
        return this.http.get(this.apiBaseUrl + '/vessel-call/' + vesselUid + '/manifest/import?masterShipmentNumber=' + filter, this.getStandardRequestOptions())
            .map(this.extractMany)
            .catch(this.handleProcessError);
    }

    private updateShipmentStatus(listUid: number[], status: string): Observable<boolean> {
        return this.http.post(this.apiBaseUrl + '/shipments/status/' + status, listUid, this.getStandardRequestOptions())
            .catch(this.handleProcessError);
    }

    public approveShipment(listUid: number[]): Observable<boolean> {
        return this.updateShipmentStatus(listUid, 'approved');
    }

    public validateShipment(listUid: number[]): Observable<boolean> {
        return this.updateShipmentStatus(listUid, 'validated');
    }

    public releaseShipment(listUid: number[]): Observable<boolean> {
        return this.updateShipmentStatus(listUid, 'released');
    }

    public unapproveShipment(listUid: number[]): Observable<boolean> {
        return this.updateShipmentStatus(listUid, 'unapproved');
    }

    public unvalidateShipment(listUid: number[]) {
        return this.updateShipmentStatus(listUid, 'unvalidated');
    }

    public holdShipment(listUid: number[]) {
        return this.updateShipmentStatus(listUid, 'held');
    }

    public handleProcessError(error: Response) {
        if (error.status == 400) {
            let text: string;
            switch (error.text()) {
                case 'Already approved':
                    text = "At least one of the selected Shipments is already approved";
                    break;
                case 'Not Validation copy':
                    text = "Validation copy of the B/L is not associated with the B/L selected for approval.";
                    break;
                case 'Already validated':
                    text = "At least one of the selected Shipments is already validated";
                    break;
                case 'Not approved':
                    text = "At least one of the selected Shipments is not approved";
                    break;
                case 'Not unvalidated':
                    text = "Cannot change the status of the selected Shipments because at least one is not unvalidated";
                    break;
                case 'Already released':
                    text = "At least one of the selected Shipments is already released";
                    break;
                case 'Already unapproved':
                    text = "At least one of the selected Shipments is already unapproved";
                    break;
                case 'Already unvalidated':
                    text = "At least one of the selected Shipments is already unvalidated";
                    break;
                case 'Already held':
                    text = "At least one of the selected Shipments is already held";
                    break;
            }
            return Observable.throw({error: text, status: error.status});
        } else {
            return super.handleError(error);
        }
    }

    /**
     * Extracts a shipment from a json string
     * 
     * @private
     * @param {Response} res 
     * @returns {Shipment} 
     * 
     * @memberOf ShipmentService
     */
    private extractOne(res: Response): Shipment {
        return new Shipment(res.json() || {});
    }

    /**
     * Extracts a list of shipments from a json string
     * 
     * @private
     * @param {Response} res 
     * @returns {Array<Shipment>} 
     * 
     * @memberOf ShipmentService
     */
    private extractMany(res: Response): Array<Shipment> {
        let result: Array<Shipment> = [];
        let jsonRes = res.json();

        if (jsonRes) {
            jsonRes.forEach((shipment: any) => {
                result.push(new Shipment(shipment));
            });
        }

        return result;
    }
}


