import { DomainObject } from './DomainObject';

/**
 * Represents a United Nations Standard Products and Services Code (UNSPSC) classification
 */
export class CommodityType extends DomainObject {
    public code: string;
    public name: string;
    public commodityFamily: any;
    public commodityClass: any;

    constructor(data?: any) {
        super(data);
        
        if (data) {
            this.code = data.code;
            this.name = data.name;
            this.commodityFamily = data.commodityFamily;
            this.commodityClass = data.commodityClass;
        }
    }
    
    public prepareData(): void {}
}



