import { DomainObject } from './DomainObject';
import { Party } from './Party';
import { Currency } from './Currency';
import { User } from './User';
import { InvoiceLineItem } from './InvoiceLineItem';
import { Payment } from './Payment';
import { PaymentReceipt } from './PaymentReceipt';
import { InvoiceCancellation } from './InvoiceCancellation';
import { Organization } from './Organization';
import { InvoiceType } from '../types/InvoiceType';
import { InvoiceStatus } from '../types/InvoiceStatus';
import { InvoicePaymentTerms } from '../types/InvoicePaymentTerms';
import { TimeUtils } from '../shared/utils/time-utils'

export class Invoice extends DomainObject {
    
    static INVOICE_PAYMENT_TERMS: Array<{value: string, name: string}> = [
        { value: "Payment_In_Advance", name: "Payment in Advance" },
        { value: "Net_7", name: "Payment seven days after invoice date" },
        { value: "Net_10", name: "Payment ten days after invoice date" },
        { value: "Net_30", name: "Payment thirty days after invoice date" },
        { value: "Net_60", name: "Payment sixty days after invoice date" },
        { value: "Net_90", name: "Payment ninty days after invoice date" },
        { value: "End_of_Month", name: "End of Month" },
        { value: "Cash_On_Delivery", name: "Cash on Delivery" },
        { value: "Cash_In_Advance", name: "Cash in Advance" },
        { value: "Cash_With_Order", name: "Cash with order" },
        { value: "Other", name: "Other" },
    ];
    static INVOICE_TYPES: Array<{value: string, name: string}> = [
        { value: "Shipment", name: "Shipment" },
        { value: "General", name: "General" }
    ];
    static INVOICE_STATUS: Array<{value: string, name: string}> = [
        { value: "New", name: "New" },
        { value: "Draft", name: "Draft" },
        { value: "Paid_In_Full", name: "Paid In Full" },
        { value: "Partially_Paid", name: "Partially Paid" },
        { value: "Cancelled", name: "Cancelled" },
    ];
    
    public invoiceNumber: string;
    public invoiceType: InvoiceType;
    public invoicePaymentTerms: InvoicePaymentTerms;
    public invoiceStatus: InvoiceStatus;
    
    public invoiceDateTime: string;
    public invoiceDate: string;
    public invoiceTime: string;
    public invoiceTotal: number;
    public currency: number | Currency;
    public currencyId: number;
    public billToParty: Party | number;
    public billToPartyId: number;
    public createdBy: number | User; // Not used on front-end
    public invoiceLineItems: Array<InvoiceLineItem> = [];
    public payments: Array<Payment> = [];
    public paymentReceipts: Array<PaymentReceipt> = [];
    public invoiceCancellation: InvoiceCancellation;
    public proFormaIndicator: boolean;
    public comments: string;
    public organization: number | Organization;
    public organizationId: number;
        
    constructor(data?: any) {
        super(data);
        
        if (data) {
            this.invoiceNumber = data.invoiceNumber;
            this.invoiceStatus = data.invoiceStatus;
            this.proFormaIndicator = data.proFormaIndicator;
            this.comments = data.comments;
            this.invoiceDateTime = data.invoiceDateTime;
            this.invoiceTotal = data.invoiceTotal;
            
            if (data.currency != null) {
                this.currency = new Currency(data.currency);
                this.currencyId = this.currency.uid;
            }
            if (data.billToParty != null) {
                this.billToParty = new Party(data.billToParty);
                this.billToPartyId = this.billToParty.uid;
            }
            if (data.createdBy != null) {
                this.createdBy = new User(data.createdBy);
            }
            if (data.invoiceCancellation) {
                this.invoiceCancellation = new InvoiceCancellation(data.invoiceCancellation);
            }
            if (data.invoiceType != null) {
                this.invoiceType = data.invoiceType;
            }
            if (data.invoicePaymentTerms) {
                this.invoicePaymentTerms = data.invoicePaymentTerms;
            }
            if (data.invoiceLineItems != null && data.invoiceLineItems.length > 0) {
                data.invoiceLineItems.forEach((lineItem: any) => {
                    this.invoiceLineItems.push(new InvoiceLineItem(lineItem));
                });
            }
            if (data.payments && data.payments.length > 0) {
                data.payments.forEach((payment: any) => {
                    this.payments.push(new Payment(payment));
                });
            }
            if (data.paymentReceipts && data.paymentReceipts.length > 0) {
                data.paymentReceipts.forEach((payment: PaymentReceipt) => {
                    this.paymentReceipts.push(new PaymentReceipt(payment));
                });
            }
            if (data.organization) {
                this.organization = new Organization(data.organization);
            }
        }
    }
    
    splitDateTimes() {
        var parts;
        if (this.invoiceDateTime) {
            parts = this.splitDateTime(this.invoiceDateTime);
            this.invoiceDate = parts[0];
            this.invoiceTime = parts[1];
        }
    }

    public prepareData() {
        if (this.invoiceDate && this.invoiceTime) {
            this.invoiceDateTime = this.invoiceDate + 'T' + TimeUtils.to24Hour(this.invoiceTime);
        }
        if (this.currencyId) {
            this.currency = this.currencyId;
        } else if (this.currency && (<Currency> this.currency).uid) {
            this.currency = (<Currency> this.currency).uid;
        }
        
        if (this.billToPartyId != null) {
            this.billToParty = this.billToPartyId;
        } else if (this.billToParty != null && (<Party> this.billToParty).uid) {
            this.billToParty = (<Party>this.billToParty).uid;
        }
        
        if (this.organizationId != null) {
            this.organization = this.organizationId;
        } else if (this.organization != null && (<Organization> this.organization).uid) {
            this.organization = (<Organization>this.organization).uid;
        }
        if (this.invoiceLineItems != null && this.invoiceLineItems.length > 0) {
            this.invoiceLineItems.forEach((lineItem: InvoiceLineItem) => {
                lineItem.prepareData();
            });
        }
        if (this.payments && this.payments.length > 0) {
            this.payments.forEach((payment: Payment) => {
                payment.prepareData();
            });
        }
        if (this.paymentReceipts && this.paymentReceipts.length > 0) {
            this.paymentReceipts.forEach((payment: PaymentReceipt) => {
                payment.prepareData();
            });
        }
    }
}



