import { DomainObject } from './DomainObject';
import { Address } from './Address';
import { PartyContact } from './PartyContact';

export class Party extends DomainObject {
    
    static PARTY_TYPES: Array<{[key: string]: string}> = [
        { key: "Carrier",           value: "Carrier" },
        { key: "ShippingLine",      value: "Shipping Line" },
        { key: "Consignee",         value: "Consignee" },
        { key: "Shipper",           value: "Shipper" },
        { key: "Agent",             value: "Agent" },
        { key: "FreightForwarder",  value: "Freight Forwarder" },
        { key: "Notify",            value: "Notify" },
        { key: "MessageSender",     value: "Message Sender" },
        { key: "Other",             value: "Other" },
    ];
    
    public partyId: string;
    public partyTypeCode: string;
    public name: string;
    public billingAddress: Address;
    public mailingAddress: Address;
    public contacts: Array<PartyContact> = [];
    public freeText: string;
        
    constructor(data?: any) {
        super(data);
        
        if (data) {
            this.partyId = data.partyId;
            this.partyTypeCode = data.partyTypeCode;
            this.name = data.name;
            this.billingAddress = new Address(data.billingAddress);
            this.mailingAddress = new Address(data.mailingAddress);
            this.freeText = data.freeText;
            
            if (data.contacts && data.contacts.length) {
                data.contacts.forEach(contact => {
                    this.contacts.push(new PartyContact(contact));
                });
            }
        }
    }
    
    public prepareData(): void {}
}


