import { Injectable, Inject } from '@angular/core';
import { AppProperties } from '../../app.properties';
import { Http, Response, URLSearchParams } from '@angular/http';
import { ChargeItem } from '../../models/ChargeItem';
import { Observable } from 'rxjs/Rx';
import { AbstractService } from '../abstract-service';

/**
 * Charge item service
 * 
 * @export
 * @class ChargeItemService
 * @extends {AbstractService}
 */
@Injectable()
export class ChargeItemService extends AbstractService {
    
    /**
     * Creates an instance of ChargeItemService.
     * @param {Http} http 
     * @param {AppProperties} appProps 
     * 
     * @memberOf ChargeItemService
     */
    constructor(private http: Http, @Inject(AppProperties) appProps: AppProperties) {
        super(appProps);
    }

    public getAll(): Observable<ChargeItem[]> {
        return this.http.get(this.apiBaseUrl + '/billing/charge-items', 
            this.getStandardRequestOptions()).map(this.extractMany).catch(this.handleError);
    }

    public getById(uid: number): Observable<ChargeItem> {        
        return this.http.get(this.apiBaseUrl + '/billing/charge-item/' + uid, 
            this.getStandardRequestOptions()).map((res) => {
                return this.extractOne(res);
        }).catch(this.handleError);
    }

    public getByFilter(filter: string): Observable<Array<ChargeItem>> {
        let options = this.getStandardRequestOptions();
        let searchParams = new URLSearchParams();
        
        searchParams.set("filter", filter);
        options.search = searchParams;
        
        return this.http.get(this.apiBaseUrl + '/billing/charge-items', options)
                .map(this.extractMany)
                .catch(this.handleError);
    }

    public getDepositChargeItems(): Observable<Array<ChargeItem>> {
        let options = this.getStandardRequestOptions();
        let searchParams = new URLSearchParams();
        
        searchParams.set("deposit_item", "true");
        options.search = searchParams;
        
        return this.http.get(this.apiBaseUrl + '/billing/charge-items', options)
                .map(this.extractMany)
                .catch(this.handleError);
    }ve
    
        public getActiveChargeItems(): Observable<Array<ChargeItem>> {
        let options = this.getStandardRequestOptions();
        let searchParams = new URLSearchParams();
        
        searchParams.set("active_item", "true");
        options.search = searchParams;
        
        return this.http.get(this.apiBaseUrl + '/billing/charge-items', options)
                .map(this.extractMany)
                .catch(this.handleError);
    }

    public getByCode(code: string): Observable<Array<ChargeItem>> {
        let options = this.getStandardRequestOptions();
        let searchParams = new URLSearchParams();
        
        searchParams.set("charge_item_code", code);
        options.search = searchParams;
        
        return this.http.get(this.apiBaseUrl + '/billing/charge-items', options)
                .map(this.extractMany)
                .catch(this.handleError);
    }
    
    public create(chargeItem: ChargeItem): Observable<ChargeItem> {
        return this.http.post(this.apiBaseUrl + '/billing/charge-items', chargeItem, 
            this.getStandardRequestOptions())
                .map(this.extractOne)
                .catch(this.handleError);
    }
    
    public update(uid: number, chargeItem: ChargeItem): Observable<ChargeItem> {
        return this.http.put(this.apiBaseUrl + '/billing/charge-item/' + uid, chargeItem, 
            this.getStandardRequestOptions())
                .map(this.extractOne)
                .catch(this.handleError);
    }
    
    public delete(uid: number): Observable<boolean> {
        return this.http.delete(this.apiBaseUrl + '/billing/charge-item/' + uid, 
            this.getStandardRequestOptions())
                .map((res: Response) => {
                    return true;
                })
                .catch(this.handleError);
    }

    private extractOne(res: Response): ChargeItem {
        return new ChargeItem(res.json() || {});
    }

    private extractMany(res: Response): Array<ChargeItem> {
        let result: Array<ChargeItem> = [];
        let jsonRes = res.json();
        
        if (jsonRes) {
            jsonRes.forEach((chargeItem: any) => {
                result.push(new ChargeItem(chargeItem));
            });
        }
        
        return result;
    }
}


