import { Currency } from './Currency';
import { DomainObject } from './DomainObject';
import { TimeUtils } from '../shared/utils/time-utils'

export class CurrencyExchangeRate extends DomainObject {
    public currency: number | Currency;
    public sellingRate: number;
    public buyingRate: number;
    
    public startDateTime: string;
    public startDate: string;
    public startTime: string;
    
    public endDateTime: string;
    public endDate: string;
    public endTime: string;
        
    constructor(data?: any) {
        super(data);
        
        if (data) {
            this.currency = new Currency(data.currency);
            this.sellingRate = data.sellingRate;
            this.buyingRate = data.buyingRate;
            
            this.startDateTime = data.startDateTime;
            this.endDateTime = data.endDateTime;
            this.splitDateTimes();
        }
    }
    
    splitDateTimes() {
        var parts;
        if (this.startDateTime) {
            parts = this.splitDateTime(this.startDateTime);
            this.startDate = parts[0];
            this.startTime = parts[1];
        }
        if(this.endDateTime) {
            parts = this.splitDateTime(this.endDateTime);
            this.endDate = parts[0];
            this.endTime = parts[1];
        }
    }
    
    public prepareData() {
        if (this.startDate) {
            this.startDateTime = this.startDate + 'T' + (this.startTime ? TimeUtils.to24Hour(this.startTime) : '00:00:00');
        }
        if (this.endDate) {
            this.endDateTime = this.endDate + 'T' + (this.endTime ? TimeUtils.to24Hour(this.endTime) : '59:99:99');
        }
    }
}



