import { Component } from '@angular/core';
import { OnInit } from '@angular/core';
import { BaseDialogComponent } from "./base-dialog.component";

import { Currency } from '../../../models/Currency';
import { CurrencyExchangeRate } from '../../../models/CurrencyExchangeRate';

import { AppPropertiesService } from "../../../services/common/app-properties.service";
import { CurrencyExchangeRateService } from "../../../services/common/currency-exchange-rate.service";

@Component({
    selector: 'exchange-rates-dialog',
    template: require('./exchange-rates-dialog.component.html'),
    providers: [
        CurrencyExchangeRateService,
        AppPropertiesService
    ]
})
export class ExchangeRatesDialogComponent extends BaseDialogComponent implements OnInit {
    public defaultCurrency: Currency;
    public tableData: Array<CurrencyExchangeRate> = [];
    public today: Date = new Date();
    
    constructor(
        protected propertiesService: AppPropertiesService,
        protected exchangeRateService: CurrencyExchangeRateService) {
        super();
    }

    ngOnInit() {
        this.propertiesService.getDefaultCurrency()
            .subscribe(currency => {
                this.defaultCurrency = currency;
        }, errors => {
            console.error(errors);
        });
        
        this.exchangeRateService.getActive()
            .subscribe((rates: Array<CurrencyExchangeRate>) => {
                this.tableData = rates;
        }, errors => {
            console.error(errors);
        });
    }
}



