import {Injectable, Inject} from '@angular/core';
import {Http, Response, URLSearchParams} from '@angular/http';
import {Observable} from 'rxjs/Rx';
import {CacheService} from 'ng2-cache';

import {AppProperties} from '../../app.properties';
import {Currency} from '../../models/Currency';
import {CurrencyExchangeRate} from '../../models/CurrencyExchangeRate';
import {AbstractService} from '../abstract-service';

/**
 * Currency service
 * 
 * @export
 * @class CurrencyService
 * @extends {AbstractService}
 */
@Injectable()
export class CurrencyService extends AbstractService {

    /**
     * Creates an instance of CurrencyService.
     * @param {Http} http 
     * @param {AppProperties} appProps 
     * 
     * @memberOf CurrencyService
     */
    constructor(
        private http: Http,
        @Inject(CacheService)
        private cacheService: CacheService,
        @Inject(AppProperties)
        appProps: AppProperties) {
        super(appProps);
    }

    public get(isCacheable: Boolean = false): Observable<Array<Currency>> {
        let uri: string = this.apiBaseUrl + '/currencies';

        return this.cacheService.exists(uri) ?
            Observable.of(<Currency[]> this.cacheService.get(uri)) :
            this.http.get(uri, this.getStandardRequestOptions())
                .map((res: Response): Array<Currency> => {
                    let currencies: Array<Currency> = this.extractMany(res);
                    // Max age is 1 minute
                    if (isCacheable)
                        this.cacheService.set(uri, currencies, {maxAge: 1 * 60});

                    return currencies;
                }).catch(this.handleError);
    }

    /**
     * Returns an {@link Observable} that emits {@link Array<Currency>}
     * @returns {Observable<Array<Currency>>} 
     * 
     * @memberOf CurrencyService
     */
    public getAll(): Observable<Array<Currency>> {
        return this.get();
    }

    /**
     * Returns an {@link Observable} that emits {@link Array<Currency>}
     * @param {string} filter 
     * @returns {Observable<Array<Currency>>} 
     * 
     * @memberOf CurrencyService
     */
    public getByFilter(filter: string): Observable<Array<Currency>> {
        let uri: string = this.apiBaseUrl + '/currencies';
        let cacheKey: string = uri + filter;
        let options = this.getStandardRequestOptions();
        let searchParams = new URLSearchParams();

        searchParams.set("filter", filter);
        options.search = searchParams;

        return this.cacheService.exists(cacheKey) ?
            Observable.of(<Currency[]> this.cacheService.get(cacheKey)) :
            this.http.get(this.apiBaseUrl + '/currencies', options)
                .map((res: Response): Array<Currency> => {
                    let currencies: Array<Currency> = this.extractMany(res);
                    // Max age is 1 minute
                    this.cacheService.set(cacheKey, currencies, {maxAge: 10 * 60});
                    return currencies;
                }).catch(this.handleError);
    }

    /**
     * Returns an {@link Observable} that emits {@link Array<Currency>}
     * @param {string} filter 
     * @returns {Observable<Array<Currency>>} 
     * 
     * @memberOf CurrencyService
     */
    public getByIsoCode(isoCode: string): Observable<Array<Currency>> {
        let options = this.getStandardRequestOptions();
        let uri: string = this.apiBaseUrl + '/currencies';
        let cacheKey: string = uri + isoCode;
        let searchParams = new URLSearchParams();

        searchParams.set("isoCode", isoCode);
        options.search = searchParams;

        return this.cacheService.exists(cacheKey) ?
            Observable.of(<Array<Currency>> this.cacheService.get(cacheKey)) :
            this.http.get(this.apiBaseUrl + '/currencies', options)
                .map((res: Response): Array<Currency> => {
                    let currencies: Array<Currency> = this.extractMany(res);
                    // Max age is 1 minute
                    this.cacheService.set(cacheKey, currencies, {maxAge: 10 * 60});
                    return currencies;
                }).catch(this.handleError);
    }

    /**
     * Returns an {@link Observable} that emits {@link Currency}
     * @param {number} uid 
     * @returns {Observable<Currency>} 
     * 
     * @memberOf CurrencyService
     */
    public getById(uid: number): Observable<Currency> {
        let uri: string = this.apiBaseUrl + '/currency/' + uid;

        return this.cacheService.exists(uri) ?
            Observable.of(<Currency> this.cacheService.get(uri)) :
            this.http.get(uri, this.getStandardRequestOptions())
                .map((res: Response): Currency => {
                    let currency: Currency = this.extractOne(res);
                    this.cacheService.set(uri, currency, {maxAge: 10 * 60});
                    return currency;
                }).catch(this.handleError);
    }

    /**
     * Returns an {@link Observable} that emits {@link Currency}
     * @param {number} uid 
     * @returns {Observable<Currency>} 
     * 
     * @memberOf CurrencyService
     */
    public getCurrentExchangeRate(uid: number): Observable<CurrencyExchangeRate> {
        let uri: string = this.apiBaseUrl + '/currency/' + uid + '/current-exchange-rate';
        return this.cacheService.exists(uri) ?
            Observable.of(<CurrencyExchangeRate> this.cacheService.get(uri)) :
            this.http.get(uri, this.getStandardRequestOptions())
                .map((res: Response): CurrencyExchangeRate => {
                    let rate: CurrencyExchangeRate = new CurrencyExchangeRate(res.json() || {});
                    this.cacheService.set(uri, rate, {maxAge: 10 * 60});
                    return rate;
                }).catch(this.handleError);
    }

    /**
     * Returns an {@link Observable} that emits {@link Currency}
     * @param {Currency} currency 
     * @returns {Observable<Currency>} 
     * 
     * @memberOf CurrencyService
     */
    public create(currency: Currency): Observable<Currency> {
        return this.http.post(this.apiBaseUrl + '/currencies', currency,
            this.getStandardRequestOptions())
            .map(this.extractOne)
            .catch(this.handleError);
    }

    /**
     * Returns an {@link Observable} that emits {@link Currency}
     * @param {number} uid 
     * @param {Currency} currency 
     * @returns {Observable<Currency>} 
     * 
     * @memberOf CurrencyService
     */
    public update(uid: number, currency: Currency): Observable<Currency> {
        return this.http.put(this.apiBaseUrl + '/currency/' + uid, currency,
            this.getStandardRequestOptions())
            .map(this.extractOne)
            .catch(this.handleError);
    }

    /**
     * Returns an {@link Observable} that emits {@link boolean}
     * @param {number} uid 
     * @returns {Observable<boolean>} 
     * 
     * @memberOf CurrencyService
     */
    public delete(uid: number): Observable<boolean> {
        return this.http.delete(this.apiBaseUrl + '/currency/' + uid,
            this.getStandardRequestOptions())
            .map((res: Response) => {
                return true;
            })
            .catch(this.handleError);
    }

    /**
     * Extracts a {@link Currency} from a json string
     * @private
     * @param {Response} res 
     * @returns {Currency} 
     * 
     * @memberOf CurrencyService
     */
    private extractOne(res: Response): Currency {
        return new Currency(res.json() || {});
    }

    /**
     * Extracts a {@link Array<Currency>} from a json string     * 
     * @private
     * @param {Response} res 
     * @returns {Array<Currency>} 
     * 
     * @memberOf CurrencyService
     */
    private extractMany(res: Response): Array<Currency> {
        let result: Array<Currency> = [];
        let jsonRes = res.json();

        if (jsonRes) {
            jsonRes.forEach((currency: any) => {
                result.push(new Currency(currency));
            });
        }

        return result;
    }
}




