import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';

import {AdvantumDatepickerDirective} from './adv-datepicker.directive';
import {AdvantumTimepickerDirective} from './adv-timepicker.directive';
import {AdvantumComboBoxDirective} from './adv-combobox.directive';
import {ComboBoxModule} from 'ng2-combobox';

@NgModule({
    imports: [
        CommonModule,
        ComboBoxModule
    ],
    declarations: [
        AdvantumDatepickerDirective,
        AdvantumTimepickerDirective,
        AdvantumComboBoxDirective,
    ],
    exports: [
        AdvantumDatepickerDirective,
        AdvantumTimepickerDirective,
        AdvantumComboBoxDirective,
        ComboBoxModule
    ]
})
export class AdvantumInputModule {}



