import { Component } from '@angular/core';
import { OnInit } from '@angular/core';
import { ChargeItemService } from "../../../services/billing/charge-item.service";
import { BaseLookupDialogComponent } from "./base-lookup-dialog.component";

@Component({
    selector: 'charge-item-lookup-dialog',
    template: require('./charge-item-lookup-dialog.component.html'),
    providers: [
        ChargeItemService
    ]
})
export class ChargeItemLookupDialogComponent extends BaseLookupDialogComponent implements OnInit {

    constructor(private chargeItemService: ChargeItemService) {
        super();
    }

    ngOnInit() {
        this.chargeItemService.getAll().subscribe((chargeItems) => {
            this.tableData = chargeItems;            
        });
    }    
    
    protected doFilter(filter: string) {
        this.chargeItemService.getByFilter(filter).subscribe((chargeItems) => {
            this.tableData = chargeItems;
            
            if (this.filterQueue.length > 0) {
                this.doFilter(this.filterQueue.pop());
            }
        });
    }
}



