import { DomainObject } from './DomainObject';

/**
 * Represents Package Types
 */
export class PackageType extends DomainObject {
    public name: string;
    public description: string;
    public code: string;
    public numericCode: number;
	
    constructor(data?: any) {
        super(data);
        
        if (data) {
            this.name = data.name;
            this.description = data.description;
            this.code = data.code;
            this.numericCode = data.numericCode;
        }
    }	  
	
    public prepareData() {
        
    }
}


