import { DomainObject } from './DomainObject';
import { ChargeItemRate } from './ChargeItemRate';

export class ChargeItem extends DomainObject {
    public code: string;
    public activeIndicator: boolean = false;
    public description: string;
    /** @todo Retrieved as domain objects from web service */
    public debitAccount: string;
    /** @todo Retrieved as domain objects from web service */
    public creditAccount: string;
    public billingGroup: string;
    public chargeItemReference: string;
    public depositItemIndicator: boolean = false;
    public depositItem: string;
    public depositChargeItem: ChargeItem | number;
    public depositChargeItemId: number;
    public systemItemIndicator: boolean = false;
    public externalChargeItem: ChargeItem | number;
    public externalChargeItemId: number;
    public rates: Array<ChargeItemRate> = new Array();
    
    private activeChargeItemRate: ChargeItemRate = null;
    
    constructor(data?:any) {
        super(data);
        
        if (data) {
            this.code = data.code;

            this.description = data.description;
            this.debitAccount = data.debitAccount;
            this.creditAccount = data.creditAccount;
            this.billingGroup = data.billingGroup;
            this.chargeItemReference = data.chargeItemReference;
            this.depositItemIndicator = data.depositItemIndicator;
            this.depositItem = data.depositItem;
            this.activeIndicator = data.activeIndicator;
            this.systemItemIndicator = data.systemItemIndicator;
            
            if (data.depositChargeItem != null) {
                this.depositChargeItem = new ChargeItem(data.depositChargeItem);
                this.depositChargeItemId = this.depositChargeItem.uid;
            }
            
            if (data.depositChargeItem != null) {
                this.externalChargeItem = new ChargeItem(data.externalChargeItem);
                this.externalChargeItemId = this.externalChargeItem.uid;
            }
            
            if (data!.rates) {
                data.rates.forEach((chargeItemRate: ChargeItemRate) => {
                    this.rates.push(new ChargeItemRate(chargeItemRate));
                });
            }
        }
    }
    
    /**
     * Returns the active charge item rate
     * @returns {@link ChargeItemRate}
     */
    public getActiveRate() {
        if (this.activeChargeItemRate) {
            return this.activeChargeItemRate;
        } else {
            if (this.rates == null || this.rates.length == 0) {
                return null;
            }
            if (this.rates.length == 1) {
                this.activeChargeItemRate = this.rates[0];
            }
            let rates: Array<ChargeItemRate> = this.rates
                .sort((a: ChargeItemRate, b: ChargeItemRate) => {
                    if (a.startDate > b.startDate) {
                        return -1;
                    }
                    if (a.startDate < b.startDate) {
                        return 1;
                    }
                    return 0;
                }
            );
            this.activeChargeItemRate = rates[0];
            
            return this.activeChargeItemRate;
        }
    }
    
    public prepareData() {
        if (this.depositChargeItemId && !this.depositItemIndicator) {
            this.depositChargeItem = this.depositChargeItemId;
        } else {
            this.depositChargeItem = null;
        }
        if (this.externalChargeItemId) {
            this.externalChargeItem = this.externalChargeItemId;
        } else {
            this.externalChargeItem = null;
        }
    }
}


