// @see http://api.jqueryui.com/datepicker/
import { Directive, ElementRef, OnInit, Input } from '@angular/core';
import { NgModel } from '@angular/forms';

declare var $: any;

@Directive({
    selector: '[advDatepicker]'
})
export class AdvantumDatepickerDirective implements OnInit {

    @Input() advDatepicker: any = {};

    constructor(private el: ElementRef, private model: NgModel) {
    }

    ngOnInit() {
        this.render();
    }
    
    render() {
        let onSelectCallbacks = [];
        let advDatepicker = this.advDatepicker || {};
        let element = $(this.el.nativeElement);

        if (advDatepicker.minRestrict) {
            onSelectCallbacks.push((selectedDate) => {
                $(advDatepicker.minRestrict).datepicker('option', 'minDate', selectedDate);
            });
        }
        if (advDatepicker.maxRestrict) {
            onSelectCallbacks.push((selectedDate) => {
                $(advDatepicker.maxRestrict).datepicker('option', 'maxDate', selectedDate);
            });
        }

        //Let others know about changes to the data field
        onSelectCallbacks.push((selectedDate) => {
            element.triggerHandler("change");

            let form = element.closest('form');

            if (typeof form.bootstrapValidator == 'function') {
                try {
                    form.bootstrapValidator('revalidateField', element);
                } catch (e) {
                    console.log(e.message)
                }
            }
        });

        let options = $.extend(advDatepicker, {
            defaultDate: new Date(),
            format: 'yy-mm-dd',
            prevText: '<i class="fa fa-chevron-left"></i>',
            nextText: '<i class="fa fa-chevron-right"></i>',
            onSelect: (selectedDate) => {
                this.model.control.setValue(selectedDate);
                
                onSelectCallbacks.forEach((callback) => {
                    callback.call(callback, selectedDate)
                })
            }
        });
        
        element.datepicker(options);
        if (advDatepicker.defaultDate && advDatepicker.defaultDate !== '') {
            element.datepicker('setDate', advDatepicker.defaultDate);
        }
    }
}


