import { DomainObject } from './DomainObject';
import { Party } from './Party';
import { MeasurementInformation } from './MeasurementInformation';
import { ContainerSize } from './ContainerSize';
import { ContainerType } from '../types/ContainerType';
import { ContainerSizeType } from './ContainerSizeType';

/**
 * Represents containers
 */
export class Container extends DomainObject {
    static CONTAINER_TYPES: Array<any> = [ 
        {value: "Reefer",    name: "Reefer"}, 
        {value: "Dry_Van",   name: "Dry Van"}, 
        {value: "High_Cube", name: "High Cube"}, 
        {value: "Tank",      name: "Tank"}
    ];
    
    public containerType: ContainerType;
    public containerSize: ContainerSize;
	
    public serialNumber: string;
    public owner: any;
    public ownerId: number;
    public sizeType: any; 
	
    // @todo: Remove once size/type maintenance is implemented
    public measurementInformation: MeasurementInformation;

    constructor(data?: any) {
        super(data);
        
        if (data) {
            this.containerType = data.containerType;
            this.containerSize = new ContainerSize(data.containerSize);
            this.serialNumber = data.serialNumber;
            
            if (data!.owner) {
                this.owner = new Party(data.owner);
                this.ownerId = this.owner.uid;
            }
            
            this.sizeType = new ContainerSizeType(data.sizeType);
            this.measurementInformation = new MeasurementInformation(data.measurementInformation);
        } else {
            this.measurementInformation = new MeasurementInformation();
        }
    }
    
    public prepareData() {
        if (this.ownerId) {
            this.owner = this.ownerId;
        }
        
        if (this.measurementInformation) {
            this.measurementInformation.prepareData();
        }
    }
}


