import { EventEmitter } from '@angular/core';
import { Output } from '@angular/core';
import { Input } from '@angular/core';
import { DomainObject } from "../../../models/DomainObject";
import { BaseDialogComponent } from "./base-dialog.component";

/**
 * Provides base class for look-up dialogs
 */
export abstract class BaseLookupDialogComponent extends BaseDialogComponent {
    public formSubmitted = false;
    public filterQuery = "";
    public isFiltering = false;
    public filterQueue: Array<string> = [];
    public selectedItems: Array<any> = [];
    public tableData: Array<any> = [];
    @Input("multiple") multiple: boolean = true;
    @Output("onSelection") onSelection: EventEmitter<any> = new EventEmitter();

    /**
     * Triggered when a record is selected
     * 
     * @param event Event details
     * @param selectedItem The selected domain object
     */
    public onChange(event: Event, selectedItem: DomainObject): void {
        let checkbox = <HTMLInputElement> event.target;
        let currentIndex = this.selectedItems.findIndex((item: DomainObject) => {
            return item.uid == selectedItem.uid;
        });
        if (this.multiple) {
            if ((currentIndex == undefined || currentIndex < 0) && checkbox.checked) {
                this.selectedItems.push(selectedItem);
            } else if (currentIndex >= 0 && !checkbox.checked) {
                this.selectedItems.splice(currentIndex, 1);
            }
        } else {
            this.selectedItems = [selectedItem];
        }
    }
    
    /**
     * Trigger when a user double-clicks a row
     * 
     * @param event Event details
     */
    public onDblClickRow(event: Event, item: any) {
        this.onSelection.emit([item]);
        this.hideDialog();
    }
    
    /**
     * Event handler for search box
     * 
     * @param event Event details
     */
    public onFilterChange(filter: string): void {
        if (this.isFiltering) {
            this.filterQueue.push(filter);
        } else {
            this.doFilter(filter);
        }
    }
    
    /**
     * Retrieves a filtered list of domain objects and assigns result to tableData
     * 
     * @param filter The text to filter on
     */
    protected abstract doFilter(filter: string);

    private onSubmit() {
        this.formSubmitted = true;
        this.onSelection.emit(this.selectedItems);
        this.tableData = []; // Reset the datatable
        this.hideDialog();
    }
}



