import {DomainObject} from "./DomainObject";
import any = jasmine.any;
import {SecretQuestion} from "./SecretQuestion";
import {UserStatus} from "./UserStatus";
import {TimeUtils} from "../shared/utils/time-utils";


export class User extends DomainObject {
    public uid: number;
    public username: string;
    public password: string = '';
    public roles: Array<any> = new Array();
    public email: string;
    public firstName: string;
    public lastName: string;
    public middleInitial: string;
    public organizations: Array<any> = new Array();
    public secretQuestion: SecretQuestion;
    public secretQuestionAnswer: string;
    public userStatus: string;
    public registrationDateTime: string;
    public expirationDate: string;

    constructor(data?: any) {
        super(data);

        if (data) {
            this.username = data.username;
            this.password = data.password;
            this.roles = data.roles;
            this.email = data.email;
            this.firstName = data.firstName;
            this.lastName = data.lastName;
            this.middleInitial = data.middleInitial;
            this.organizations = data.organizations;
            this.secretQuestion = new SecretQuestion(data.secretQuestion);
            this.secretQuestionAnswer = data.secretQuestionAnswer;
            this.userStatus = data.userStatus;
            this.registrationDateTime = data.registrationDateTime;
            this.expirationDate = data.expirationDate;
        }
    }

    public prepareData(): void {
    }

    public getReadableName(): string {
        let fullName = '';
        fullName = this.firstName? this.firstName : '';
        fullName += ' ' + (this.lastName? this.lastName : '');
        if (fullName.trim())
            return fullName;
        return this.username;
    }
}


