import { PaymentMethod } from './PaymentMethod';
import { Bank } from './Bank';
import { ChequeType } from './ChequeType';
import { Party } from './Party';

export class Cheque extends PaymentMethod {
    public chequeNumber: string;
    public chequeDate: string;
    public bank: number | Bank;
    public bankId: number;
    public chequeType: number | ChequeType;
    public chequeTypeId: number;
    public customer: number | Party;
    public customerId: number;
        
    constructor(data?: any) {
        super(data);
        
        this.paymentMethodType = "Cheque";
        
        if (data) {
            this.chequeNumber = data.chequeNumber;
            this.chequeDate = data.chequeDate;
            
            if (data.bank) {
                this.bank = new Bank(data.bank);
                this.bankId = this.bank.uid;
            } else if (data.bankId) {
                this.bankId = data.bankId;
            }
            
            if (data.chequeType) {
                this.chequeType = new ChequeType(data.chequeType);
                this.chequeTypeId = this.chequeType.uid;
            } else if (data.chequeTypeId) {
                this.chequeTypeId = data.chequeTypeId;
            }
            
            if (data.customer) {
                this.customer = new Party(data.customer);
                this.customerId = this.customer.uid;
            } else if (data.cardTypeId) {
                this.customerId = data.customerId;
            }
        }
    }
    
    public prepareData(): void {            
        if (this.bank instanceof Bank) {
            this.bank = this.bank.uid;
        } else if (this.bankId) {
            this.bank = this.bankId;
        }
            
        if (this.chequeType instanceof ChequeType) {
            this.chequeType = this.chequeType.uid;
        } else if (this.chequeTypeId) {
            this.chequeType = this.chequeTypeId;
        }

        if (this.customer instanceof Party) {
            this.customer = this.customer.uid;
        } else if (this.customerId) {
            this.customer = this.customerId;
        }
    }
}


