import { DomainObject } from './DomainObject';
import { PaymentCancellation } from './PaymentCancellation';
import { PaymentLineItem } from './PaymentLineItem';
import { InvoiceLineItemPayment } from './InvoiceLineItemPayment';
import { PaymentReceipt } from './PaymentReceipt';
import { Invoice } from './Invoice';
import { User } from './User';
import { Party } from './Party';
import { Currency } from './Currency';
import { CurrencyExchangeRate } from './CurrencyExchangeRate';
import { PaymentMethod } from './PaymentMethod';

/**
 * Represents an invoice payment
 */
export class Payment extends DomainObject {
    
    public paymentType: "Invoice" | "Advance";
    public paymentStatus: "Uncollected" | "Collected" = "Uncollected";
    public paidBy: number | Party;
    public paidById: number;
    public receivedBy: User;
    public paymentTotal: number;
    public paymentDate: string;
    public paymentCancellation: PaymentCancellation;
    public paymentReceipt: PaymentReceipt;
    public paymentLineItems: Array<PaymentLineItem> = new Array();
    public invoiceLineItemPayments: Array<InvoiceLineItemPayment> = new Array();
    public invoice: number | Invoice;
    public invoiceId: number;
    public currency: number | Currency;
    public currencyId: number;
    public currencyExchangeRate: number | CurrencyExchangeRate;
    public currencyExchangeRateId: number;

    constructor(data?: any) {
        super(data);
        
        if (data) {
            this.paymentStatus = data.paymentStatus;
            this.paymentType = data.paymentType;
            if (data.paidBy) {
                this.paidBy = new Party(data.paidBy);
                this.paidById = this.paidBy.uid;
            }
            this.receivedBy = data.receivedBy instanceof User ? data.receivedBy : new User(data.receivedBy);
            
            // @todo Ensure that we check to see if we already have a domain object before creating a new one!
            if (data.invoice) {
                this.invoice = data.invoice instanceof Invoice ? data.invoice : new Invoice(data.invoice);
            }
            this.paymentTotal = data.paymentTotal;
            this.paymentDate = data.paymentDate;
            
            if (data.paymentCancellation) {
                this.paymentCancellation = 
                    new PaymentCancellation(data.paymentCancellation);
            }
            if (data.paymentReceipt) {
                this.paymentReceipt = 
                    new PaymentReceipt(data.paymentReceipt);
            }
            if (data.invoiceLineItemPayments) {
                data.invoiceLineItemPayments.forEach(item => {
                    this.invoiceLineItemPayments
                        .push(new InvoiceLineItemPayment(item));
                })
            }
            if (data.paymentLineItems) {
                data.paymentLineItems.forEach(item => {
                    this.paymentLineItems
                        .push(new PaymentLineItem(item));
                })
            }
            if (data.chargePaymentAllocations) {
                data.chargePaymentAllocations.forEach(item => {
                    this.invoiceLineItemPayments
                        .push(new InvoiceLineItemPayment(item));
                })
            }
            if (data.currency != null) {
                this.currency = new Currency(data.currency);
                this.currencyId = this.currency.uid;
            }
            if (data.currencyExchangeRate)  {
                this.currencyExchangeRate = new CurrencyExchangeRate(data.currencyExchangeRate);
            }
        }
    } 

    public prepareData() {        
        if (this.invoice instanceof Invoice) {
            this.invoice = this.invoice.uid;
        } else {
            this.invoice = null;
        }
        if (this.paidBy instanceof Party) {
            this.paidBy = this.paidBy.uid;
        } else {
            this.paidBy = this.paidById;
        }
        if (this.currency instanceof Currency) {
            this.currency = this.currency.uid;
        } else {
            this.currency = this.currencyId;
        }
        if (this.currencyExchangeRate instanceof CurrencyExchangeRate) {
            this.currencyExchangeRate = this.currencyExchangeRate.uid;
        } else {
            this.currencyExchangeRate = this.currencyExchangeRateId;
        }
        if (this.paymentLineItems) {
            this.paymentLineItems.forEach((item: PaymentLineItem) => {
                if (item.paymentMethod instanceof PaymentMethod) {
                    item.paymentMethod.prepareData();
                }
            })
        }
        this.receivedBy = null;
    }
}



