import { DomainObject } from './DomainObject';
import {VesselCall} from './VesselCall';

const CHANGE_TYPE_REJECTION = 'Rejection';
const CHANGE_TYPE_REOPEN = 'Reopen';
const CHANGE_TYPE_CANCEL = 'Cancellation';

export class VesselCallStateChange extends DomainObject {
    
    public uid: number;
    public stateChangeType: string;
    public vesselCall: any;
    public reason: string;
    public changeDateTime: string;
    
    // The following are not a part of domain object
    public vesselCallId: number;
    public changeDate: string;
    public changeTime: string;
        
    constructor(data?: any) {
        super(data);
        
        if (data) {
            this.stateChangeType = data.stateChangeType;
            this.vesselCall = data.vesselCall;
            this.vesselCallId = data.vesselCall != null ? data.vesselCall.uid : null;

            this.reason = data.reason;
            this.changeDateTime = data.changeDateTime;
            this.splitDateTimes();
        }
    }
    
    splitDateTimes() {
        let parts;
        if (this.changeDateTime) {
            parts = this.splitDateTime(this.changeDateTime);
            this.changeDate = parts[0];
            this.changeTime = parts[1];
        }
    }
    
    public prepareData() {
        if (this.changeDate && this.changeTime) {
            this.changeDateTime = this.changeDate + 'T' + this.changeTime;
        }
    }
    
    /**
     * Splits dates in the following format: yyyy-MM-dd'T'HH:mm:ss.SSS
     */
    splitDateTime(dt: String) {        
        return dt.split('T');
    }
    
    public exchangeData(data: any) {
        Object.keys(data).forEach(key => {
            this[key] = data[key];
        });
    }
}


