/**
 * A base for pipes that display enum human-readable values
 */
export class EnumBasePipe {
    protected values: Array<{value: string, name: string}>;

    transform(value: any, args?: any): any {
        if (!value || !this.values) {
            return null;
        }

        let selectedType: {value: string, name: string} = this.values.find(
            (entry: {value: string, name: string}, index: number, obj: Array<any>): boolean => {
                return entry.value == value;
            }
        );

        return selectedType ? selectedType.value : null;
    }

}



