import { DomainObject } from './DomainObject';
import { MeasurementInformation } from './MeasurementInformation';
import { PackageType } from './PackageType';

export class PackagingInformation extends DomainObject {
    public packageType: number | PackageType;
    public packageTypeId: number;
    
    public description: string; // Describes marks and numbers
    public measurementInformation: MeasurementInformation = new MeasurementInformation();
        
    constructor(data?: any) {
        super(data);
        
        this.measurementInformation = new MeasurementInformation();
        if (data) {
            this.description = data.description;
            
            if (data.packageType) {
                this.packageType = new PackageType(data.packageType);
                this.packageTypeId = this.packageType.uid;
            } else {
                this.packageType = this.packageTypeId = null;
            }
            
            if (data.measurementInformation) {
                this.measurementInformation = new MeasurementInformation(data.measurementInformation);
            }
        } else {
            this.packageType = this.packageTypeId = null;
        }
    }
    
    public prepareData(): void {
        if (this.packageType instanceof PackageType) {
            this.packageType = this.packageType.uid;
        } else {
            this.packageType = this.packageTypeId;
        }
        this.measurementInformation.prepareData();
    }
}



