import { DomainObject } from './DomainObject';

export class ContainerType extends DomainObject {
    public uid: number;
    public name: string;
	public code: string;
	public description: string;
    
    constructor(data?: any) {
        super();
        
        if (data) {
            this.uid = data.uid;
            this.name = data.name;
			this.code = data.code;
			this.description = data.description;
        }
    } 

    public prepareData() {}
}



