import { Pipe, PipeTransform } from '@angular/core';
import { EnumBasePipe } from './enum-base.pipe'
import { Invoice } from '../../models/Invoice'

@Pipe({
    name: 'invoicetype'
})
export class InvoiceTypePipe extends EnumBasePipe implements PipeTransform {

    constructor() {
        super();
        this.values = Invoice.INVOICE_TYPES
    }

}



