import { DomainObject } from './DomainObject';
import { Party } from './Party';
import { InvoiceLineItemCharge } from './InvoiceLineItemCharge';
import { Invoice } from './Invoice';

/**
 * Represents invoice line item
 */
export class InvoiceLineItem extends DomainObject {
    static LINE_ITEM_TYPE: Array<{[key: string]: string}> = [
        { value: 'Shipment', name: 'Shipment'},
        { value: 'General', name: 'General'},
    ];
    public lineItemType: "Shipment" | "General" = "Shipment"
    public referenceNumber: string;
    public invoiceLineItemCharges: Array<InvoiceLineItemCharge> = [];
    public receivingParty: Party; // @TODO: Remove
    public chargeAmount: number = 0; // @TODO: Remove
    public invoice: number | Invoice;

    constructor(data?: any) {
        super(data);
        
        if (data) {
            this.lineItemType = data.lineItemType;
            this.referenceNumber = data.referenceNumber;
            this.receivingParty = data.receivingParty;
            
            if (data.invoice) {
                this.invoice = new Invoice(data.invoice);
            }
            
            if (data.invoiceLineItemCharges && data.invoiceLineItemCharges.length > 0) {
                data.invoiceLineItemCharges.forEach((invoiceLineItemCharge: InvoiceLineItemCharge) => {
                    this.invoiceLineItemCharges.push(new InvoiceLineItemCharge(invoiceLineItemCharge));
                });
            }
        }
    }

    public prepareData() {
        if (this.invoice && this.invoice instanceof Invoice) {
            this.invoice = (<Invoice>this.invoice).uid;
        }
    }
}



