import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';

import { AdvantumInputModule } from "../advantum-input/advantum-input.module";

import { InlineEditorComponent, InputComponents } from './inline-editor.component';

@NgModule({
  imports: [CommonModule, FormsModule, AdvantumInputModule],
  declarations: [InlineEditorComponent, ...InputComponents],
  exports: [InlineEditorComponent, AdvantumInputModule]
})
export class InlineEditorModule { }



