import { Component } from '@angular/core';
import { EventEmitter } from '@angular/core';
import { Output } from '@angular/core';
import { Input } from '@angular/core';

import { BaseDialogComponent } from "./base-dialog.component";

@Component({
    selector: 'confirmation-dialog',
    template: require('./confirmation-dialog.component.html'),
})
export class ConfirmationDialogComponent extends BaseDialogComponent {
    @Input('title') dialogTitle: string;
    @Input('message') confirmationQuestion: string;
    @Input('yesValue') yesValue: any = true;
    @Input('noValue') noValue: any = false;
    @Output() onResponse: EventEmitter<any> = new EventEmitter();

    ngOnInit() {}
    
    private onClickYes() {
        this.onResponse.emit(this.yesValue);
        this.hideDialog();
    }
    
    private onClickNo() {
        this.onResponse.emit(this.noValue);
        this.hideDialog();
    }
}



