import { DomainObject } from './DomainObject';
import { User } from './User';

/**
 * Class to represent a general cancellation
 */
export class Cancellation extends DomainObject {
    public cancellationDateTime: string;
    public cancellationDate: string;
    public cancellationTime: string;
    
    public cancellationReason: string;
    public responsibleUser: User;
        
    constructor(data?: any) {
        super(data);
        
        if (data) {
            this.cancellationDateTime = data.cancellationDateTime;
            this.cancellationReason = data.cancellationReason;
            
            if (data.responsibleUser != null) {
                this.responsibleUser = new User(data.responsibleUser);
            }
            this.splitDateTimes();
        }
    }
    
    splitDateTimes() {
        var parts;
        if (this.cancellationDateTime) {
            parts = this.splitDateTime(this.cancellationDateTime);
            this.cancellationDate = parts[0];
            this.cancellationTime = parts[1];
        }
    }

    public prepareData() {}
}



