import { DomainObject } from './DomainObject';
import { Measurement } from './Measurement';
import { MotorVehicleModel } from './MotorVehicleModel';
import { MotorVehicleManufacturer } from './MotorVehicleManufacturer';

export class MotorVehicle extends DomainObject {
    public chassisNumber: string;
    public weight: Measurement = new Measurement();
    public yearOfManufacture: number;
    
    public motorVehicleManufacturer: number | MotorVehicleManufacturer = null;
    public motorVehicleManufacturerId: number = null;
    
    public motorVehicleModel: number | MotorVehicleModel = null;
    public motorVehicleModelId: number = null;
    
    constructor(data?: any) {
        super(data);
        
        if (data) {
            this.chassisNumber = data.chassisNumber;
            this.yearOfManufacture = data.yearOfManufacture;
            
            if (data.weight) {
                this.weight = new Measurement(data.weight);
            }
            if (data.motorVehicleModel) {
                this.motorVehicleModel = new MotorVehicleModel(data.motorVehicleModel);
                this.motorVehicleModelId = this.motorVehicleModel.uid;
            }            
            if (data.motorVehicleManufacturer) {
                this.motorVehicleManufacturer = new MotorVehicleManufacturer(data.motorVehicleManufacturer);
                this.motorVehicleManufacturerId = this.motorVehicleManufacturer.uid;
            }
        }
    }
    
    public prepareData(): void {
        if (this.motorVehicleModel instanceof MotorVehicleModel) {
            this.motorVehicleModel = this.motorVehicleModel.uid;
        } else {
            this.motorVehicleModel = this.motorVehicleModelId;
        }
        if (this.motorVehicleManufacturer instanceof MotorVehicleManufacturer) {
            this.motorVehicleManufacturer = this.motorVehicleManufacturer.uid;
        } else {
            this.motorVehicleManufacturer = this.motorVehicleManufacturerId;
        }
        if (this.weight instanceof Measurement) {
            this.weight.prepareData();
        }
    }
}


