import {
    Component,
    OnInit,
    Input
} from '@angular/core';
import { Address } from "../../../models/Address";

/**
 * Party address view component
 * 
 * @export
 * @class PartyAddressViewComponent
 * @implements {OnInit}
 */
@Component({
    selector: 'party-address',
    template: require('./party-address.component.html')
})
export class PartyAddressViewComponent implements OnInit {
    @Input('partyAddress')
    public partyAddress: Address;
    @Input('viewType')
    public viewType: "table" | "text" = "table";
    
    /**
     * Creates an instance of PartyAddressViewComponent.
     * 
     * @memberOf PartyAddressViewComponent
     */
    constructor() {}

    /**
     * @inheritdoc
     * @memberOf PartyAddressViewComponent
     */
    public ngOnInit(): void {}
}


