import {NgModule} from "@angular/core";
import {FormsModule} from "@angular/forms";
import {ModalModule} from 'ng2-bootstrap';
import {CommonModule} from "@angular/common";

import { InlineEditorModule } from "../shared/forms/inline-editor";
import { DialogModule } from "../shared/component/dialog/dialog.module";

import { PartyAddressViewComponent } from "../shared/component/view/party-address.component";

@NgModule({
  imports: [
    FormsModule, 
    CommonModule,
    ModalModule,
    InlineEditorModule,
    DialogModule
  ],
  declarations: [
    PartyAddressViewComponent,
  ],
  exports: [
    PartyAddressViewComponent,
    InlineEditorModule,
    DialogModule
  ]

})
export class AdvantumModule {}



