import { DomainObject } from './DomainObject';

export class CardBrand extends DomainObject {
    public name: string;
        
    constructor(data?: any) {
        super(data);
        
        if (data) {
            this.name = data.name;
        }
    }
    
    public prepareData(): void {}
}


