import { PaymentMethod } from './PaymentMethod';
import { CardType } from './CardType';
import { CardBrand } from './CardBrand';

export class Card extends PaymentMethod {
    public authorizationCode: string;
    public cardBrand: number | CardBrand;
    public cardBrandId: number;
    public cardType: number | CardType;
    public cardTypeId: number;
    
    constructor(data?: any) {
        super(data);
        
        this.paymentMethodType = "Card";
        
        if (data) {
            this.authorizationCode = data.authorizationCode;
            
            if (data.cardType) {
                this.cardType = new CardType(data.cardType);
                this.cardTypeId = this.cardType.uid;
            } else if (data.cardTypeId) {
                this.cardTypeId = data.cardTypeId;
            }
            
            if (data.cardBrand) {
                this.cardBrand = new CardType(data.cardBrand);
                this.cardBrandId = this.cardBrand.uid;
            } else if (data.cardBrandId) {
                this.cardBrandId = data.cardBrandId;
            }
        }
    }
    
    public prepareData(): void {
        if (this.cardType instanceof CardType) {
            this.cardType = this.cardType.uid;
        } else if (this.cardTypeId) {
            this.cardType = this.cardTypeId;
        }

        if (this.cardBrand instanceof CardBrand) {
            this.cardBrand = this.cardBrand.uid;
        } else if (this.cardBrandId) {
            this.cardBrand = this.cardBrandId;
        }
    }
}


