import { DomainObject } from './DomainObject';
import { ChargeProfileChargeItem } from './ChargeProfileChargeItem';
import { ChargeItem } from './ChargeItem';
import { ContainerSize } from './ContainerSize';
import { ContainerType } from './ContainerType';

/**
 * Represents a charge profile
 */
export class ChargeProfile extends DomainObject {
    public code: string;
    public activeIndicator: boolean = false;
    public description: string;
    public conversionFeeItem: ChargeItem | number;
    public conversionFeeItemId: number;
    public chargeProfileChargeItems: Array<ChargeProfileChargeItem> = new Array();
    public sizeTypeIndicator: boolean = false;
    public containerSize: ContainerSize | number = null;
    public containerSizeId: number = null;
    public containerType: ContainerType | number = null;
    public containerTypeId: number = null;
    
    constructor(data?: any) {
        super(data);
        
        if (data) {
            this.code = data.code;
            this.activeIndicator = data.activeIndicator;
            this.description = data.description;
            this.sizeTypeIndicator = data.sizeTypeIndicator;
            
            if (data.containerSize) {
                this.containerSize = new ContainerSize(data.containerSize);
                this.containerSizeId = this.containerSize.uid;
            }
            
            if (data.containerType) {
                this.containerType = new ContainerType(data.containerType);
                this.containerTypeId = this.containerType.uid;
            }
            
            if (data.conversionFeeItem)  {
                this.conversionFeeItem = new ChargeItem(data.conversionFeeItem);
                this.conversionFeeItemId = this.conversionFeeItem.uid;
            }
            
            if (data!.chargeProfileChargeItems) {
                data.chargeProfileChargeItems.forEach((chargeProfileChargeItem) => {
                    this.chargeProfileChargeItems.push(
                                new ChargeProfileChargeItem(chargeProfileChargeItem));
                });
            }
        }
    }
    
    public prepareData() {
        if (this.conversionFeeItemId) {
            this.conversionFeeItem = this.conversionFeeItemId;
        }
        if (this.containerSizeId) {
            this.containerSize = this.containerSizeId;
        }
        if (this.containerTypeId) {
            this.containerType = this.containerTypeId;
        }
    }
}


