// @see https://select2.github.io
import { Directive, ElementRef, OnInit, Input } from '@angular/core';
import { NgModel } from '@angular/forms';
import { EventEmitter } from '@angular/core';
import { Output } from '@angular/core';

declare var $: any;

@Directive({
    selector: '[advComboBox]'
})
export class AdvantumComboBoxDirective implements OnInit {
    @Input('advComboBox') public advComboBox: any = {};
    @Output() onSelection: EventEmitter<any> = new EventEmitter();
    
    private defaultOptions = {
    }

    constructor(private el: ElementRef, private model: NgModel) { }

    ngOnInit() {
        System.import('script-loader!select2/dist/js/select2.full.min.js').then(() => {
            setTimeout(() => {
                this.render();
            }, 500);
        })
    }

    render() {
        $(this.el.nativeElement).select2($.extend(this.advComboBox, this.defaultOptions));
        if (this.model) {
            let select2 = $(this.el.nativeElement).parent().find(".select2");
            select2.addClass(this.model.invalid ? 'ng-invalid' : 'ng-valid');
        }
        $(this.el.nativeElement).on("select2:select", (e: any) => {
            if (this.model && e.params && e.params.data && e.params.data.id) {
                // Angular has values in select options as "index: id"
                let itemId = e.params.data.id.split(': ')[1];
                this.model.control.setValue(itemId);
                let select2 = $(this.el.nativeElement).parent().find(".select2");
                select2.removeClass('ng-invalid');
                select2.removeClass('ng-valid');
                select2.addClass(this.model.invalid ? 'ng-invalid' : 'ng-valid');
                this.onSelection.emit(itemId);
            }
        });
    }

}



