import { DomainObject } from "./DomainObject";
import { TimeUtils } from "../shared/utils/time-utils";

export class SecuritySessionConfiguration extends DomainObject{

    public maxSessionDurationHours: number;
    public sessionInactivityTimeoutMinutes: number;
    public scheduledSessionTermination: string;
    public scheduledSessionTerminatedReason: string;
    public checkMaxSessionDuration: boolean;
    public checkInactivityTimeout: boolean;

    public sessionTerminationDate: string;
    public sessionTerminationTime: string;

    constructor(data?: any) {
        super();

        if (data){
            this.maxSessionDurationHours = data.maxSessionDurationHours;
            this.sessionInactivityTimeoutMinutes = data.sessionInactivityTimeoutMinutes;
            this.scheduledSessionTermination = data.scheduledSessionTermination;
            this.scheduledSessionTerminatedReason = data.scheduledSessionTerminatedReason;
            this.checkMaxSessionDuration = data.checkMaxSessionDuration;
            this.checkInactivityTimeout = data.checkInactivityTimeout;
            this.splitDateTimes();
        }
    }

    splitDateTimes() {
        var parts;
        if(this.scheduledSessionTermination) {
            parts = this.splitDateTime(this.scheduledSessionTermination);
            this.sessionTerminationDate = parts[0];
            this.sessionTerminationTime = parts[1];
        }
    }

    public prepareData() {
        if(this.sessionTerminationDate && this.sessionTerminationTime){
            this.scheduledSessionTermination = this.sessionTerminationDate + 'T' + TimeUtils.to24Hour(this.sessionTerminationTime);
        }
    }

    splitDateTime(dt:string) {
        return dt.split("T");
    }

    public exchangeData(data) {
        Object.keys(data).forEach((key: any) => {
            this[key] = data[key];
        });
    }
}



