import { DomainObject } from './DomainObject';
import { PaymentMethod } from "./PaymentMethod";
import { Payment } from './Payment';
import { Card } from './Card';
import { Cash } from './Cash';
import { Cheque } from './Cheque';
import { WireTransfer } from './WireTransfer';
import { AccountWithdrawal } from './AccountWithdrawal';

/**
 * Represents an invoice payment
 */
export class PaymentLineItem extends DomainObject {
    public payment: number | Payment;
    public paymentId: number;
    public paymentMethod: number | PaymentMethod = new PaymentMethod({paymentType: 'Cash'});
    public paymentAmount: number;

    constructor(data?: any) {
        super(data);
        
        if (data) {
            
            if (data.paymentMethod && typeof !data.paymentMethod !== "object") {
                switch(data.paymentMethod.paymentMethodType) {
                    case "Card":
                        this.paymentMethod = new Card(data.paymentMethod);
                    break;
                    case "Cheque":
                        this.paymentMethod = new Cheque(data.paymentMethod);
                    break;
                    case "Wire_Transfer":
                        this.paymentMethod = new WireTransfer(data.paymentMethod);
                    break;
                    case "Account_Withdrawal":
                        this.paymentMethod = new AccountWithdrawal(data.paymentMethod);
                    break;
                    case "Cash":
                    default:
                        this.paymentMethod = new Cash(data.paymentMethod);
                }
            } else if (data.paymentMethod instanceof PaymentMethod) {
                this.paymentMethod = data.paymentMethod;
            }
            
            // @todo Ensure that we check to see if we already have a domain object before creating a new one!
            this.payment = data.payment instanceof Payment ? data.payment : new Payment(data.payment);
            this.paymentAmount = data.paymentAmount;
        }
    } 

    public prepareData() {
        if (this.payment instanceof Payment) {
            this.payment = this.payment.uid;
        } else {
            this.payment = null;
        }
        if (this.paymentMethod && this.paymentMethod instanceof PaymentMethod) {
            this.paymentMethod.prepareData();
        }
    }
}



