import { DomainObject } from './DomainObject';
import { MotorVehicleModel } from './MotorVehicleModel';

export class MotorVehicleManufacturer extends DomainObject {
    public name: string;
    public motorVehicleModels: Array<MotorVehicleModel> = [];
    
    constructor(data?: any) {
        super(data);
        
        if (data) {
            this.name = data.name;
            
            if (data.motorVehicleModels instanceof Array) {
                data.motorVehicleModels.forEach((model: MotorVehicleModel) => {
                    this.motorVehicleModels.push(new MotorVehicleModel(model));
                });
            }
        }
    }
    
    public prepareData(): void {
    }
}


