import {DomainObject} from './DomainObject';
import {ChargeItem} from './ChargeItem';
import {ChargeProfile} from './ChargeProfile';
import {CalculationPeriodDateType} from '../types/CalculationPeriodDateType';

/**
 * Association class for charge group and charge item
 */
export class ChargeProfileChargeItem extends DomainObject {
    static CALCULATION_PERIOD_DATE_TYPES: Array<any> = [
        {value: 'Today',          name: 'Today'},
        {value: 'Storage_Due',    name: 'Storage Due'},
        {value: 'Storage_End',    name: 'Storage End'},
        {value: 'Demurrage_Due',  name: 'Demurrage Due'},
        {value: 'Demurrage_End',  name: 'Demurrage End'},
        {value: 'Vessel_Arrive',  name: 'Vessel Arrive'},
        {value: 'Vessel_Depart',  name: 'Vessel Depart'},
        {value: 'Stripping_Date', name: 'Stripping Date'},
        {value: 'Stuffing_Date',  name: 'Stuffing Date'}
    ];

    // Start
    public startPeriodDateType: CalculationPeriodDateType;
    public startApplyAfterWorkingDays: number = 0;
    public startAddedDays: number = 0;
    public startExcludeHolidaysIndicator: boolean = false;
    public startExcludeWeekendsIndicator: boolean = false;
    // End
    public endPeriodDateType: CalculationPeriodDateType;
    public endApplyAfterWorkingDays: number = 0;
    public endAddedDays: number = 0;
    public endExcludeHolidaysIndicator: boolean = false;
    public endExcludeWeekendsIndicator: boolean = false;
    
    public printableIndicator: boolean = true;
    public chargeProfile: ChargeProfile | number;
    public chargeProfileId: number;
    public chargeItem: ChargeItem | number;
    public chargeItemId: number;
    
    public precedence: number = null;

    constructor(data?: any) {
        super(data);

        if (data) {
            this.startPeriodDateType = data.startPeriodDateType;
            this.startApplyAfterWorkingDays = data.startApplyAfterWorkingDays;
            this.startAddedDays = data.startAddedDays;
            this.startExcludeHolidaysIndicator = data.startExcludeHolidaysIndicator;
            this.startExcludeWeekendsIndicator = data.startExcludeWeekendsIndicator;

            this.endPeriodDateType = data.endPeriodDateType;
            this.endApplyAfterWorkingDays = data.endApplyAfterWorkingDays;
            this.endAddedDays = data.endAddedDays;
            this.endExcludeHolidaysIndicator = data.endExcludeHolidaysIndicator;
            this.endExcludeWeekendsIndicator = data.endExcludeWeekendsIndicator;
            
            this.printableIndicator = data.printableIndicator;
            this.chargeProfile = new ChargeProfile(data.chargeProfile);
            this.chargeProfileId = this.chargeProfile.uid;
            this.chargeItem = new ChargeItem(data.chargeItem);
            this.chargeItemId = this.chargeItem.uid;
            
            this.precedence = data.precedence;
        }
    }

    public prepareData() {
        if (this.chargeItemId) {
            this.chargeItem = this.chargeItemId;
        }
        if (this.chargeProfileId) {
            this.chargeProfile = this.chargeProfileId;
        }
    }
}


