import { DomainObject } from './DomainObject';

/**
 * Represents an invoice payment
 */
export class PaymentMethod extends DomainObject {
    static PAYMENT_METHOD_TYPES: Array<{[key: string]: string}> = [
        { value: 'Cash', name: 'Cash' },
        { value: 'Card', name: 'Card' },
        { value: 'Cheque', name: 'Cheque' },
        { value: 'Wire_Transfer', name: 'Wire Transfer' },
        { value: 'Account_Withdrawal', name: 'Account Withdrawal' }
    ];
    
    public paymentMethodType: "Cash" | "Card" | "Cheque" | "Wire_Transfer" | "Account_Withdrawal";
    
    constructor(data?: any) {
        super(data);
    } 

    public prepareData() {
    }
    
    public static getPaymentMethodTypeName(paymentMethodType: "Cash" | "Card" 
        | "Cheque" | "Wire_Transfer" | "Account_Withdrawal") { // @TODO Convert to type
        let name: string = null;
        PaymentMethod.PAYMENT_METHOD_TYPES.forEach(entry => {
            if (entry["value"] == paymentMethodType) {
                name = entry["name"];
            }
        });
        return name;
    }
}



