import { EventEmitter } from '@angular/core';
import { ModalDirective } from "ng2-bootstrap";
import { ViewChild } from "@angular/core/src/metadata/di";

/**
 * Provides a base class for dialogs
 */
export abstract class BaseDialogComponent {
    @ViewChild('modalDialog')
    public modalDialog: ModalDirective;
    protected _state: EventEmitter<any> = new EventEmitter();
    
    get state(): EventEmitter<any> {
        return this._state;
    }

    /**
     * Opens dialog and emits this component
     */
    public showDialog(): void {
        this.modalDialog.show();
        this._state.emit("opened");
    }

    /**
     * Closes dialog and emits this component
     */
    public hideDialog(): void {
        this.modalDialog.hide();
        this._state.emit("closed");
    }
}



