import {Component, ViewChild, EventEmitter, Output, Input} from "@angular/core";
import {ModalDirective} from "ng2-bootstrap";
import {ChangePasswordService} from "../../../services/user/change-password.service";
import {NotificationService} from "../../utils/notification.service";
import {SecurityLevelConfiguration} from "../../../models/SecurityLevelConfiguration";
import {AuthService} from "../../../+auth/auth.service";

@Component({
    selector: 'change-password-dialog',
    template: require('./change-password-dialog.component.html'),
    providers: [ChangePasswordService, NotificationService, AuthService]
})

export class ChangePasswordDialogComponent{
    @ViewChild('selectionModal')
    public selectionModal: ModalDirective;

    @Input('securityLevelConfiguration')
    public securityLevelConfiguration: SecurityLevelConfiguration = new SecurityLevelConfiguration();

    @Input('username')
    public username: string;
    public oldPassword: string;
    @Output('password')
    public password: string;
    public repeatPassword: string;
    public passwordRequirements: Array<string> = new Array();

    @Output() onResponse: EventEmitter<any> = new EventEmitter();

    public emailAsUserName: boolean = false;
    private passwordMinimumLength: number;
    private checkMinimumLength: boolean;

    private oldPasswordError: boolean = false;

    public passwordStrengthConfiguration: any = {};

    public constructor(private changePasswordService: ChangePasswordService, private notificationService: NotificationService, private authService: AuthService){

    }

    public showModal(): void {

        this.passwordRequirements.push("contain letters");
        if(this.securityLevelConfiguration.includeNumbersAndLetters){
            this.passwordRequirements.push("contain numbers");
        }
        if(this.securityLevelConfiguration.includeSpecialCharacters){
            this.passwordRequirements.push("contain a special character");
        }
        if(this.securityLevelConfiguration.checkMinimumLength){
            this.passwordRequirements.push("contain at least " + this.securityLevelConfiguration.minimumPasswordLength + " characters");
        }
        if(this.securityLevelConfiguration.excludeRepeatingCharacters){
            this.passwordRequirements.push("not contain repeating characters");
        }
        this.emailAsUserName = this.securityLevelConfiguration.emailAsUserName;
        this.passwordMinimumLength = this.securityLevelConfiguration.minimumPasswordLength;
        this.checkMinimumLength = this.securityLevelConfiguration.checkMinimumLength;
        this.selectionModal.show();
    }

    public hideModal(): void {
        this.selectionModal.hide();
    }

    public savePassword() {
        this.changePasswordService.changePassword(this.username, this.oldPassword, this.password).subscribe(()=>{
            this.notificationService.smartMessageBox({
                title: "<i class='fa fa-check txt-color-greenLight'></i> Password has been changed",
                content: "The password for " + this.username + " has been successfully changed",
                buttons: '[Ok]'},
                ()=>{
                    this.onResponse.emit({result: "success", password:this.password});
                    this.hideModal();
            });
        }, (errors) => {
            if (errors && errors.status == 401) {
                this.oldPasswordError = true;
            }
        });
    }

    public setPasswordStrengthIndicator() {
        var colors: any = {"strong":"#739e73", "good":"#3266b1", "fair":"#c79121", "weak":"#a90329"};
        var label: any = {"strong":"Strong", "good":"Good", "fair":"Fair", "weak":"Weak", "short":"Too short (at least 7 characters)"};
        var widths: any = {"strong":"100%", "good":"75%", "fair":"50%", "weak":"25%", "short":"0"};
        var passwordStrength: string = this.passwordStrength(this.checkMinimumLength ? this.passwordMinimumLength : 10);

        this.passwordStrengthConfiguration.style = {
            "background-color": colors[passwordStrength],
            "width" :widths[passwordStrength]
        };
        this.passwordStrengthConfiguration.label = label[passwordStrength];
        this.passwordStrengthConfiguration.strength = passwordStrength;
    }

    private passwordStrength(passwordMinimumLength: number){
        var pass: string = this.password;
        if(pass.length > passwordMinimumLength
            && this.numberOfSpecialCharacters(pass) > 1
            && this.numberOfRepeatingCharacter(pass) == 0
            && this.numberOfLetters(pass) >= 1
            && this.numberOfNumbers(pass) >= 1 ){
            return "strong";
        } else if(pass.length >= passwordMinimumLength
            && this.numberOfSpecialCharacters(pass) >= 1
            && this.numberOfRepeatingCharacter(pass) == 0
            && this.numberOfLetters(pass) >= 1
            && this.numberOfNumbers(pass) >= 1) {
            return "good";
        } else if(pass.length == passwordMinimumLength
            && this.numberOfSpecialCharacters(pass) == 1
            && this.numberOfRepeatingCharacter(pass) <= 1
            && this.numberOfLetters(pass) >= 1
            && this.numberOfNumbers(pass) == 1) {
            return "fair";
        } else if (pass.length >= 7){
            return "weak";
        } else {
            return "short";
        }
    }

    private numberOfSpecialCharacters(value:string){
        if (!this.securityLevelConfiguration.includeSpecialCharacters)
            return 99;
        var cont:number = 0;
        for(var i:number=0; i<value.length; i++){
            if(value.charAt(i).match('[!"#$%&\'()*+,-./:;<=>?@\\[\\]\^_`{|\\}~]')){
                cont++;
            }
        }
        return cont;
    }

    private numberOfRepeatingCharacter(value:string){
        if (!this.securityLevelConfiguration.excludeRepeatingCharacters)
            return 0;
        var cont:number = 0;
        for (var i: number=0; i<value.length-1; i++){
            if(value.charAt(i).toUpperCase() == value.charAt(i+1).toUpperCase()){
                cont++;
            }
        }
        return cont;
    }

    private numberOfLetters(value:string){
        var cont:number = 0;
        for (var i: number=0; i<value.length-1; i++) {
            if(value.charAt(i).match('[a-zA-Z]')){
                cont++;
            }
        }
        return cont;
    }

    private numberOfNumbers(value: string) {
        var cont:number = 0;
        for (var i: number=0; i<value.length-1; i++) {
            if(value.charAt(i).match('[0-9]')){
                cont++;
            }
        }
        return cont;
    }

}



