import {DomainObject} from './DomainObject';
import {ChargeItem} from './ChargeItem';
import {Container} from './Container';
import {Currency} from './Currency';
import {PayType} from './PayType';
import {Shipment} from './Shipment';

export class ShipmentSpecificCharge extends DomainObject {
    static PAY_TYPE: Array<string> = [
        "Prepaid", "Collect"
    ];
    public chargeAmount: number;
    public chargeItem: ChargeItem | string | number;
    public chargeItemId: number;
    public container: Container | string | number;
    public containerId: number;
    public currency: Currency | string | number;
    public currencyId: number;
    public payType: "Prepaid" | "Collect";
    public quantity: number;
    public shipment: Shipment | number;
    public shipmentId: number;
    public systemDefined: boolean;
    public total:number;

    constructor(data?: any) {
        super(data);

        if (data) {
            this.chargeAmount = data.chargeAmount;
            this.chargeItem = data.chargeItem == null ? null : new ChargeItem(data.chargeItem);
            this.chargeItemId = data.chargeItem == null ? null : this.chargeItem.uid;
            this.container = data.container == null ? null : new Container(data.container);
            this.containerId = data.container == null ? null : this.container.uid;
            this.currency = data.currency == null ? null : new Currency(data.currency);
            this.currencyId = data.currency == null ? null : this.currency.uid;
            this.payType = data.payType;
            this.quantity = data.quantity;
            this.shipment = new Shipment(data.shipment);
            this.shipmentId = this.shipment.uid;
            this.systemDefined = data.systemDefined;
            this.total = this.quantity * this.chargeAmount;
        }
    }

    public prepareData() {
        if (this.currencyId) {
            this.currency = this.currencyId;
        }
        if (this.chargeItemId) {
            this.chargeItem = this.chargeItemId;
        }
        if (this.containerId) {
            this.container = this.containerId;
        }
    }
}


