import { DomainObject } from './DomainObject';
import { InvoiceLineItemCharge } from './InvoiceLineItemCharge';
import { Payment } from './Payment';
import { CurrencyExchangeRate } from './CurrencyExchangeRate';

export class ChargePaymentAllocation extends DomainObject {
    public amountPaid: number = 0;
    public invoiceLineItemCharge: number | InvoiceLineItemCharge;
    public payment: number | Payment;
    public currencyExchangeRate: number | CurrencyExchangeRate;
    public currencyExchangeRateId: number;
    
    constructor(data?: any) {
        super(data);
        
        if (data) {
            this.amountPaid = data.amountPaid;
            if (data.invoiceLineItemCharge) {
                this.invoiceLineItemCharge = new InvoiceLineItemCharge(data.invoiceLineItemCharge);
            }
            if (data.payment) {
                this.payment = new Payment(data.payment);
            }
            if (data.currencyExchangeRate)  {
                this.currencyExchangeRate = new CurrencyExchangeRate(data.currencyExchangeRate);
            }
        }
    }
    
    public prepareData(): void {
        if (this.invoiceLineItemCharge instanceof InvoiceLineItemCharge) {
            this.invoiceLineItemCharge = this.invoiceLineItemCharge.uid;
        }
        if (this.payment instanceof Payment) {
            this.payment = this.payment.uid;
        }
        if (this.currencyExchangeRate instanceof CurrencyExchangeRate) {
            this.currencyExchangeRate = this.currencyExchangeRate.uid;
        } else {
            this.currencyExchangeRate = this.currencyExchangeRateId;
        }
    }
}


