import { Injectable, Inject } from '@angular/core';
import { AppProperties } from '../../app.properties';
import { Http, Response, URLSearchParams } from '@angular/http';
import { Consignee } from '../../models/Consignee';
import { Observable } from 'rxjs/Rx';
import { AbstractService } from '../abstract-service';

/**
 * Consignee service
 * 
 * @export
 * @class ConsigneeService
 * @extends {AbstractService}
 */
@Injectable()
export class ConsigneeService extends AbstractService {
    
    /**
     * Creates an instance of ConsigneeService.
     * @param {Http} http 
     * @param {AppProperties} appProps 
     * 
     * @memberOf ConsigneeService
     */
    constructor(private http: Http, @Inject(AppProperties) appProps: AppProperties) {
        super(appProps);
    }

    /**
     * Returns a list of consignee
     * 
     * @returns {Observable<Array<Consignee>>} 
     * 
     * @memberOf ConsigneeService
     */
    public get(): Observable<Array<Consignee>> {
        return this.http.get(this.apiBaseUrl + '/consignees', this.getStandardRequestOptions())
                .map(this.extractMany)
                .catch(this.handleError);
    }
    
    /**
     * Returns a list of consignee
     * 
     * @returns {Observable<Array<Consignee>>} 
     * 
     * @memberOf ConsigneeService
     */
    public getAll(): Observable<Array<Consignee>> {
        return this.http.get(this.apiBaseUrl + '/consignees', this.getStandardRequestOptions())
                .map(this.extractMany)
                .catch(this.handleError);
    }

    /**
     * Returns a filtered list of consignee
     * 
     * @param {string} filter 
     * @returns {Observable<Array<Consignee>>} 
     * 
     * @memberOf ConsigneeService
     */
    public getByFilter(filter: string): Observable<Array<Consignee>> {
        let options = this.getStandardRequestOptions();
        let searchParams = new URLSearchParams();
        
        searchParams.set("filter", filter);
        options.search = searchParams;
        
        return this.http.get(this.apiBaseUrl + '/consignees', options)
                .map(this.extractMany)
                .catch(this.handleError);
    }

    /**
     * Returns a consignee identified by uid
     * 
     * @param {number} uid 
     * @returns {Observable<Consignee>} 
     * 
     * @memberOf ConsigneeService
     */
    public getById(uid: number): Observable<Consignee> {
        return this.http.get(this.apiBaseUrl + '/consignee/' + uid, this.getStandardRequestOptions())
                .map((res) => {return this.extractOne(res);})
                .catch(this.handleError);
    }
    
    private extractOne(res: Response): Consignee {
        return (res.json() || {}) as Consignee;
    }

    private extractMany(res: Response): Array<Consignee> {
        let result: Array<Consignee> = [];
        let jsonRes = res.json();
        
        if (jsonRes) {
            jsonRes.forEach((party: any) => {
                result.push(new Consignee(party));
            });
        }
        
        return result;
    }
}


