// @see http://jdewit.github.io/bootstrap-timepicker/
import { Directive, ElementRef, OnInit, Input } from '@angular/core';
import { NgModel } from '@angular/forms';

declare var $: any;

@Directive({
    selector: '[advTimepicker]'
})
export class AdvantumTimepickerDirective implements OnInit {
    @Input('advTimepicker') public advTimepicker: any = {};
    
    private defaultOptions = {
        defaultTime: 'current',
        disableFocus: false,
        disableMousewheel: false,
        isOpen: false,
        minuteStep: 1,
        modalBackdrop: false,
        orientation: { x : 'auto', y : 'auto' },
        secondStep: 15,
        snapToStep: false,
        showInputs: true,
        showMeridian: false,
        showSeconds: false,
        template: 'dropdown'
    }

    constructor(private el: ElementRef, private model: NgModel) { }

    ngOnInit() {
        System.import('script-loader!bootstrap-timepicker/js/bootstrap-timepicker.min.js').then(() => {
            setTimeout(() => {
                this.render();
            }, 500);
        });
    }

    render() {
        $(this.el.nativeElement).timepicker($.extend(this.advTimepicker, this.defaultOptions));
        $(this.el.nativeElement).timepicker().on('changeTime.timepicker', (e) => {
            if (this.model) {
                this.setValue(this.el.nativeElement);
            }
        });
        
        if (this.model) {
            this.setValue(this.el.nativeElement);
        }
    }
    
    private setValue(el: any) {
        let timepicker = $(el).data("timepicker");
        if (!this.advTimepicker.showMeridian) {
            let time = timepicker.getTime();
            this.model.control.setValue(time);
            $(el).val(time);
        } else {
            this.model.control.setValue(timepicker.getTime());
        }
    }
}



