import { DomainObject } from './DomainObject';

export class UnitOfMeasure extends DomainObject {
    public uid: number;
    public name: string;
    public isoCode: string;
    public isoSymbol: string;
        
    constructor(data?: any) {
        super(data);
        
        if (data) {
            this.name = data.name;
            this.isoCode = data.isoCode;
            this.isoSymbol = data.isoSymbol;
        }
    }
    
    public prepareData(): void {}
}


