import { DomainObject } from './DomainObject';

export class ContainerSizeType extends DomainObject {
    public uid: number;
	public code: string;
	public legacyCode: string;
	public type: any;
	public size: any;
	public description: string;
	
    constructor(data?: any) {
        super();
        
        if (data) {
            this.uid = data.uid;
			this.code = data.code;
			this.legacyCode = data.legacyCode;
			this.type = data.type;
			this.size = data.size;
			this.description = data.description;
        }
    } 

    public prepareData() {}
}



