import { DomainObject } from './DomainObject';

export class ContainerSize extends DomainObject {
    public name: string;
    public code: string;
    public description: string;
        
    constructor(data?: any) {
        super(data);
        
        if (data) {
            this.name = data.name;
            this.code = data.code;
            this.description = data.description;
        }
    } 

    public prepareData() {}
}



