import { DomainObject } from './DomainObject';
import {UnitOfMeasure} from './UnitOfMeasure';

export class Measurement extends DomainObject {
    public measurementValue: number = 0;
    public unitOfMeasure: UnitOfMeasure | number | string;
    public unitOfMeasureId: number | string;
        
    constructor(data?: any) {
        super(data);
        
        if (data) {
            this.measurementValue = data.measurementValue;
            if (data.unitOfMeasure) {
                this.unitOfMeasure = new UnitOfMeasure(data.unitOfMeasure);
                this.unitOfMeasureId = this.unitOfMeasure.uid;
            }
        }
    }
    
    public prepareData() {
        if (this.unitOfMeasureId) {
            this.unitOfMeasure = this.unitOfMeasureId;
        }
    }
}


