import { Cancellation } from './Cancellation';
import { Payment } from './Payment';

/**
 * Represents the cancellation of an invoice payment
 */
export class PaymentCancellation extends Cancellation {
    public payment: number | Payment;
    public paymentId: number;
        
    constructor(data?: any) {
        super(data);
        
        if (data) {
            if (data.payment) {
                this.payment = new Payment(data.payment);
                this.paymentId = this.payment.uid;
            }
        }
    }
    
    public prepareData() {
        if (this.payment instanceof Payment) {
            this.payment = this.payment.uid;
        } else {
            this.payment = this.paymentId;
        }
    }
}



