import { PaymentMethod } from './PaymentMethod';
import { DepositAccount } from './DepositAccount';

export class AccountWithdrawal extends PaymentMethod {
    public withdrawalDateTime: string;
    public withdrawalAmount: number;
    public depositAccount: number | DepositAccount;
    public depositAccountId: number;
        
    constructor(data?: any) {
        super(data);
        
        this.paymentMethodType = "Account_Withdrawal";
        
        if (data) {
            this.withdrawalDateTime = data.withdrawalDateTime;
            this.withdrawalAmount = data.withdrawalAmount;
            
            if (data.depositAccount) {
                this.depositAccount = new DepositAccount(data.depositAccount);
                this.depositAccountId = this.depositAccount.uid;
            } else if (data.depositAccountId) {
                this.depositAccountId = data.depositAccountId;
            }
        }
    }
    
    public prepareData(): void {
        if (this.depositAccount instanceof DepositAccount) {
            this.depositAccount = this.depositAccount.uid;
        } else {
            this.depositAccount = this.depositAccountId;
        }
    }
}


