import {Component, OnInit, ViewChild} from "@angular/core";
import {Router, ActivatedRoute} from "@angular/router";
import {AuthService} from "../auth.service";
import {NgForm} from "@angular/forms";
import {AuthGuard} from "../../core/guards/auth-guard";
import {ChangePasswordDialogComponent} from "../../shared/component/dialog/change-password-dialog.component";
import {SecurityLevelConfiguration} from "../../models/SecurityLevelConfiguration";
import {NotificationService} from "../../shared/utils/notification.service";
import {SessionUtil} from "./session-util/session-util";
import {SessionConfigurationService} from "../../services/session/session-configuration.service";

@Component({
    selector: 'app-login',
    template: require('./login.component.html'),
    providers: [AuthService, NotificationService, AuthGuard, SessionConfigurationService]
})
export class LoginComponent implements OnInit {

    private username: string;
    public password: string;

    private useEmailAsUsername: boolean;
    private maxLoginAttempts: number = 9999;
    private loginAttempts: number = 0;
    private reCaptchaResponse: string = '';
    @ViewChild('captchaControl')
    private reCaptchaField: any;
    @ViewChild('loginForm')
    private currentForm: NgForm;
    @ViewChild(ChangePasswordDialogComponent)
    private changePasswordLookupDialogComponent: ChangePasswordDialogComponent;
    private capsLockActive: boolean;
    private rememberUser: boolean;
    private returnUrl: string = '/dashboard/analytics';
    private focusedComponent: string = '';
    public securityLevelConfiguration: SecurityLevelConfiguration;

    private static staticRouter: Router;

    constructor(private router: Router, private route: ActivatedRoute, private notificationService: NotificationService, private authService: AuthService, private sessionConfigurationService: SessionConfigurationService) {
        LoginComponent.staticRouter = this.router;
    }

    ngOnInit() {
        AuthGuard.logout();
        this.loginAttempts = 0;
        this.authService.getSecurityConfiguration().subscribe((securityConfiguration) => {
            this.securityLevelConfiguration = securityConfiguration;
            if (securityConfiguration.checkLoginAttempts)
                this.maxLoginAttempts = securityConfiguration.loginAttemptsBeforeCaptcha;
            this.useEmailAsUsername = securityConfiguration.emailAsUserName;
        });
        this.returnUrl = this.route.snapshot.queryParams['returnUrl'] || this.returnUrl;
        this.checkLoginErrors();
    }

    private checkLoginErrors() {
        var errorQueryParam = this.route.snapshot.queryParams['error'];
        if (errorQueryParam && errorQueryParam == 'loginExceeded') {
            NotificationService.instance.smartMessageBox({
                title: "<i class='fa fa-warning txt-color-red'></i> Multiple Sessions",
                content: "An existing session has been detected using the same login credentials. If you continue, that session will be terminated.<br>" +
                "Do you want to sign in anyway?",
                buttons: '[No][Yes]'
            }, (btn)=>{
                if ('Yes' == btn) {
                    this.authService.checkLoginUser(true).subscribe((user) => {
                        this.router.navigate([this.returnUrl])
                    }, (errors) => {
                        console.log(errors);
                    });
                }
                console.log(btn);
            });
        }
    }

    public changePassword(){
        this.changePasswordLookupDialogComponent.showModal();
    }

    login() {
        if (!this.username || !this.password)
            return;
        if (this.loginAttempts > this.maxLoginAttempts) {
            this.authService.verifyCaptcha(this.reCaptchaResponse).subscribe(()=> {
                this.doLogin();
            }, (errors) => {
                this.reCaptchaResponse = null;
                console.log(errors);
            });
        } else {
            this.doLogin();
        }
    }

    public onChangePasswordLookupComponentClose(response: any): void{
        if (response != null) {
            if(response.result === "success"){
                this.password = response.password;
                this.doLogin();
            }
        }
    }

    private doLogin() {
        this.authService.login(this.username, this.password).subscribe((accessToken)=> {
            if (this.rememberUser) {
                localStorage.setItem('currentUser', JSON.stringify(accessToken));
            }
            AuthGuard.storeCurrentUserToken(accessToken);
            this.authService.checkLoginUser(false).subscribe((user) => {
                this.sessionConfigurationService.getSessionSecurityConfiguration()
                    .subscribe((securitySessionConfiguration) => {
                        SessionUtil.sessionTimeInSeconds = securitySessionConfiguration.maxSessionDurationHours*60*60;
                        SessionUtil.instance.idleTimeInSeconds = securitySessionConfiguration.sessionInactivityTimeoutMinutes*60;
                        SessionUtil.instance.start();
                        SessionUtil.idleHanlder.subscribe((text)=> {
                                if(text === 'endSession'){
                                    this.authService.logout().subscribe(()=> {
                                        this.router.navigate(['/auth/login']);
                                    }, (errors)=> {
                                        this.router.navigate(['/auth/login']);
                                    });
                                }
                            }
                        );
                        this.router.navigate([this.returnUrl])
                    }, (errors) => {
                        console.dir(errors);
                    })
            }, (errors) => {
                if (errors.error == "maximum_sessions") {
                    NotificationService.instance.smartMessageBox({
                        title: "<i class='fa fa-warning txt-color-red'></i> Multiple Sessions",
                        content: "An existing session has been detected using the same login credentials. If you continue, that session will be terminated.<br>" +
                        "Do you want to sign in anyway?",
                        buttons: '[No][Yes]'
                    }, (btn)=>{
                        if ('Yes' == btn) {
                            this.authService.checkLoginUser(true).subscribe((user) => {
                                this.router.navigate([this.returnUrl])
                            }, (errors) => {
                                console.log(errors);
                            });
                        }
                        console.log(btn);
                    });
                }
            });

        }, (errors) => {
            if (errors.status == 401) {
                this.handleUnauthorizedError(errors);
            }
            if (errors.error == 'invalid_grant' && errors.error_description.toLowerCase() == 'user credentials have expired') {
                this.changePassword();
                return;
            }
            this.password = '';
            if (this.loginAttempts > this.maxLoginAttempts) {
                this.reCaptchaField.reset();
            }
            this.loginAttempts++;
        });
    }

    public markCapsLockActive(event: KeyboardEvent) {
        if (event.which == 20) {
            this.capsLockActive = !this.capsLockActive;
        } else {
            this.capsLockActive = this.checkCapsLockActive(event);
        }
        return true;
    }

    /**
     * Using solution from {@link http://stackoverflow.com/a/896515/7444342}
     * @param event
     * @returns {boolean}
     */
    private checkCapsLockActive(event: KeyboardEvent) {
        var s = event.key ? event.key : String.fromCharCode(event.which);
        if (s.toUpperCase() === s && s.toLowerCase() !== s && !event.shiftKey) {
            return true;
        }
        return false;
    }

    private handleUnauthorizedError(errors: any) {
        if (errors.message && errors.message.toLowerCase() === 'user is disabled') {
            this.notificationService.smartMessageBox({
                title: "<i class='fa fa-warning txt-color-red'></i> Unable to Login",
                content: "User account is inactive. Please contact your System Administrator for assistance.",
                buttons: '[OK]'
            });
        }
    }

    static navigate() {
        if (LoginComponent.staticRouter)
            LoginComponent.staticRouter.navigate(['auth', 'login'], {queryParams:{error : 'loginExceeded'}})
    }
}



