import { DomainObject } from './DomainObject';
import { Currency } from './Currency';
import { Container } from './Container';

/**
 * Represents the cacluation of a charge item charge for a invoice item
 */
export class InvoiceLineItemCharge extends DomainObject {
    /**
     * Reference to the charge item this charge refers to
     * @TODO: Determine if we want a hard or soft reference here
     */
    public referenceNumber: string;
    public description: string;
    public fromDate: string;
    public toDate: string;
    public paidToDate: string;
    /**
     * Total charges to-date or the product of {@link quantity} and {@link chargeRate}
     */
    public chargeAmount: number;
    public discount: number;
    /**
     * The amount the customer wants to pay
     * @deprecated To be removed in future iteration
     */
    public amountBeingPaid: number;
    /**
     * The total amount due to pay
     */
    public amountDue: number;
    public currency: number | Currency;
    public currencyId: number;
    /**
     * The charge rate, if any
     */
    public chargeRate: number;
    /**
     * The floating point number of times this charge is applied
     */
    public quantity: number = 1.00;
    
    constructor(data?: any) {
        super(data);
        
        if (data) {
            this.referenceNumber = data.referenceNumber;
            this.description = data.description;
            this.fromDate = data.fromDate;
            this.toDate = data.toDate;
            this.paidToDate = data.paidToDate;
            this.chargeAmount = data.chargeAmount ? data.chargeAmount : 0;
            this.discount = data.discount ? data.discount : 0;
            this.amountDue = data.amountDue ? data.amountDue : 0;
            
            if (data.amountBeingPaid) {
                this.amountBeingPaid = data.amountBeingPaid > this.amountDue ? this.amountDue : data.amountBeingPaid;
            } else {
                this.amountBeingPaid = 0;
            }
            
            this.chargeRate = data.chargeRate ? data.chargeRate : 0;
            this.quantity = data.quantity ? data.quantity : 1.00;
            
            if (data.currency)  {
                this.currency = new Currency(data.currency);
                this.currencyId = this.currency.uid;
            }
        }
    } 

    public prepareData() {
        if (this.currency && this.currency instanceof Currency) {
            this.currency = this.currency.uid;
        } else {
            this.currency = this.currencyId;
        }
    }
}



