import {Injectable, Inject} from "@angular/core";
import {AbstractService} from "../abstract-service";
import {Http, Response} from "@angular/http";
import {AppProperties} from "../../app.properties";
import {SecuritySessionConfiguration} from "../../models/SecuritySessionConfiguration";
import {Observable} from "rxjs";

@Injectable()
export class SessionConfigurationService extends AbstractService{

    constructor(private http: Http, @Inject(AppProperties) appProps: AppProperties) {
        super(appProps);
    }

    public getSessionSecurityConfiguration(): Observable<SecuritySessionConfiguration>{
        return this.http.get(this.apiBaseUrl + '/security-session-configuration', this.getStandardRequestOptions())
            .map(this.extractOne)
            .catch(this.handleError);
    }

    public updateSessionSecurityConfiguration(securitySessionConfiguration: SecuritySessionConfiguration): Observable<SecuritySessionConfiguration>{
        return this.http.put(this.apiBaseUrl + '/security-session-configuration', securitySessionConfiguration,
            this.getStandardRequestOptions())
            .map(this.extractOne)
            .catch(this.handleError);
    }

    private extractOne(res: Response): SecuritySessionConfiguration {
        return new SecuritySessionConfiguration(res.json() || {});
    }
}



