import { DomainObject } from './DomainObject';
import { Invoice } from './Invoice';
import { Payment } from './Payment';

/**
 * Represents an invoice payment receipt
 */
export class PaymentReceipt extends DomainObject {
    public receiptNumber: string;
    public invoice: Invoice;
    public payment: Payment;
    public receiptType: "Shipment" | "General" | "Advance";
        
    constructor(data?: any) {
        super(data);
        
        if (data) {
            this.receiptNumber = data.receiptNumber;
            
            if (data.invoice != null) {
                this.invoice = new Invoice(data.invoice);
            }
            if (data.payment) {
                this.payment = new Payment(data.payment);
            }
            this.receiptType = data.receiptType;
        }
    }

    public prepareData() {}
}



