import {Country} from './Country';
import {Berth} from './Berth';
import { DomainObject } from './DomainObject';

export class Port extends DomainObject {
    public uid: number;
    public code: string;
    public description: string;
    public city: string;
    public country: any;
    public berths: Array<Berth> = [];
    public display : string;
        
    constructor(data?: any) {
        super(data);
        
        if (data) {
            this.code = data.code;
            this.description = data.description;
            this.city = data.city;
            this.country = new Country(data.country);
            this.display = this.code + " (" + this.description + ")"; 
            if (data.berths && data.berths.length) {
                data.berths.forEach(berth => {
                    this.berths.push(new Berth(berth));
                });
            }
        }
    }
    
    public prepareData(): void {
		
	}
}


