import { DomainObject } from './DomainObject';
import { Measurement } from './Measurement';

export class MeasurementInformation extends DomainObject {
    public exteriorHeight: Measurement = new Measurement();
    public exteriorWidth: Measurement = new Measurement();
    public exteriorLength: Measurement = new Measurement();
    public weight: Measurement = new Measurement();
    public volume: Measurement = new Measurement();
        
    constructor(data?: any) {
        super(data);
        
        if (data) {
            if (data.exteriorHeight) {
                this.exteriorHeight = new Measurement(data.exteriorHeight);
            }
            if (data.exteriorWidth) {
                this.exteriorWidth = new Measurement(data.exteriorWidth);
            }
            if (data.exteriorLength) {
                this.exteriorLength = new Measurement(data.exteriorLength);
            }
            if (data.weight) {
                this.weight = new Measurement(data.weight);
            }
            if (data.volume) {
                this.volume = new Measurement(data.volume);
            }
        }
    }
    
    public prepareData() {
        if (this.exteriorHeight) {
            this.exteriorHeight.prepareData();
        }
        if (this.exteriorWidth) {
            this.exteriorWidth.prepareData();
        }
        if (this.exteriorLength) {
            this.exteriorLength.prepareData();
        }
        if (this.weight) {
            this.weight.prepareData();
        }
        if (this.volume) {
            this.volume.prepareData();
        }
    }
}


