import { DomainObject } from './DomainObject';
import { InvoiceLineItem } from './InvoiceLineItem';
import { ChargePaymentAllocation } from './ChargePaymentAllocation';
import { Payment } from './Payment';

/**
 * Represents the payment amount associated with an invoice line item
 */
export class InvoiceLineItemPayment extends DomainObject {
    
    public amountPaid: number;
    public invoiceLineItem: number | InvoiceLineItem;
    public chargePaymentAllocations: Array<ChargePaymentAllocation> = new Array();
    public payment: Payment;

    constructor(data?: any) {
        super(data);
        
        if (data) {
            this.amountPaid = data.amountPaid;
            
            if (data.invoiceLineItem) {
                this.invoiceLineItem = new InvoiceLineItem(data.invoiceLineItem);
            }
            
            if (data.payment) {
                this.payment = new Payment(data.payment);
            }
            
            if (data.chargePaymentAllocations) {
                data.chargePaymentAllocations.forEach(item => {
                    this.chargePaymentAllocations
                        .push(new ChargePaymentAllocation(item));
                })
            }
        }
    } 

    public prepareData() {
        if (this.invoiceLineItem instanceof InvoiceLineItem) {
            this.invoiceLineItem = this.invoiceLineItem.uid;
        } else {
            this.invoiceLineItem = null;
        }
    }
}



