import { Component } from '@angular/core';
import { OnInit } from '@angular/core';
import { ShipmentService } from "../../../services/manifest/shipment/shipment.service";
import { BaseLookupDialogComponent } from "./base-lookup-dialog.component";

/**
 * Shipment lookup dialog component
 */
@Component({
    selector: 'shipment-lookup-dialog',
    template: require('./shipment-lookup-dialog.component.html'),
    providers: [
        ShipmentService
    ]
})
export class ShipmentLookupDialogComponent extends BaseLookupDialogComponent implements OnInit {

    constructor(private shipmentService: ShipmentService) {
        super();
    }

    ngOnInit() {
        this.shipmentService.getAll().subscribe((shipments) => {
            this.tableData = shipments;            
        });
    }

    protected doFilter(filter: string) {
        this.shipmentService.getByFilter(filter).subscribe((shipments) => {
            this.tableData = shipments;
            
            if (this.filterQueue.length > 0) {
                this.doFilter(this.filterQueue.pop());
            }
        });
    }
}



