import {Port} from './Port';
import {Measurement} from './Measurement';
import { DomainObject } from './DomainObject';

export class Berth extends DomainObject {
    public uid: number;
    public code: string;
    public port: any;
    public depth: Measurement;
        
    constructor(data?: any) {
        super(data);
        
        if (data) {
            this.code = data.code;
            this.port = new Port(data.port);
            this.depth = new Measurement(data.depth);
        }
    }
    
    public prepareData(): void {}
}


