import { Injectable, Inject } from '@angular/core';
import { AppProperties } from '../../app.properties';
import { Http, Response, URLSearchParams } from '@angular/http';
import { VesselCall } from '../../models/VesselCall';
import { VesselCallStateChange } from '../../models/VesselCallStateChange';
import { Observable } from 'rxjs/Rx';
import { AbstractService } from '../abstract-service';

@Injectable()
export class VesselCallService extends AbstractService {
    
    constructor(private http: Http, @Inject(AppProperties) appProps: AppProperties) {
        super(appProps);
    }

    public getAll(): Observable<Array<VesselCall>> {
        return this.http.get(this.apiBaseUrl + '/vessel-calls', this.getStandardRequestOptions())
                    .map(this.extractMany)
                    .catch(this.handleError);
    }

    public getByFilter(filter: string): Observable<Array<VesselCall>> {
        let options = this.getStandardRequestOptions();
        let searchParams = new URLSearchParams();
        
        searchParams.set("filter", filter);
        options.search = searchParams;
        
        return this.http.get(this.apiBaseUrl + '/vessel-calls', options)
                .map(this.extractMany)
                .catch(this.handleError);
    }

    public getByVoyageNumber(voyageNumber: string): Observable<Array<VesselCall>> {
        let options = this.getStandardRequestOptions();
        let searchParams = new URLSearchParams();
        
        searchParams.set("voyageNumber", voyageNumber); // @TODO: Use underscores in query parameter key
        options.search = searchParams;
        
        return this.http.get(this.apiBaseUrl + '/vessel-calls', options)
                .map(this.extractMany)
                .catch(this.handleError);
    }

    public getByVessel(vesselUid: number):Observable<Array<VesselCall>> {
        return this.http.get(this.apiBaseUrl + '/vessel-calls/vessel/search?vessel='+vesselUid, this.getStandardRequestOptions())
            .map(this.extractMany)
            .catch(this.handleError);

    }

    public getById(uid: number): Observable<VesselCall> {
        return this.http.get(this.apiBaseUrl + '/vessel-call/' + uid, this.getStandardRequestOptions())
                    .map(this.extractOne)
                    .catch(this.handleError);
    }
    
    public create(vesselCall: VesselCall): Observable<VesselCall> {
        return this.http.post(this.apiBaseUrl + '/vessel-calls', vesselCall, 
            this.getStandardRequestOptions())
                .map(this.extractOne)
                .catch(this.handleError);
    }
    
    public update(uid: number, vesselCall: VesselCall): Observable<VesselCall> {
        return this.http.put(this.apiBaseUrl + '/vessel-call/' + uid, vesselCall, 
            this.getStandardRequestOptions())
                .map(this.extractOne)
                .catch(this.handleError);
    }
    
    public finalize(uid: number): Observable<VesselCall> {
        return this.http.put(this.apiBaseUrl + '/vessel-call/' + uid + '/finalize', {}, 
            this.getStandardRequestOptions())
                .map(this.extractOne)
                .catch(this.handleError);
    }
    
    public reject(vesselCallReviewRejection: VesselCallStateChange): Observable<VesselCallStateChange> {
        return this.http.put(this.apiBaseUrl + '/vessel-call/' + vesselCallReviewRejection.vesselCallId + '/reject', vesselCallReviewRejection, 
            this.getStandardRequestOptions())
            .map((res: Response) => { return new VesselCallStateChange(res.json() || {}); })
                .catch(this.handleError);
    }
    
    public cancel(vesselCallCancellation: VesselCallStateChange): Observable<VesselCallStateChange> {
        return this.http.put(this.apiBaseUrl + '/vessel-call/' + vesselCallCancellation.vesselCallId + '/cancel', vesselCallCancellation, 
            this.getStandardRequestOptions())
            .map((res: Response) => { return new VesselCallStateChange(res.json() || {}); })
                .catch(this.handleError);
    }
    
    private extractOne(res: Response): VesselCall {
        return new VesselCall(res.json() || {});
    }

    private extractMany(res: Response): Array<VesselCall> {
        let result: Array<VesselCall> = [];
        let jsonRes = res.json();
        
        if (jsonRes) {
            jsonRes.forEach((vesselCall: any) => {
                result.push(new VesselCall(vesselCall));
            });
        }
        
        return result;
    }
}


