import { Injectable } from '@angular/core';
import { Inject } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { Observer } from 'rxjs/Observer';
import { AppProperties } from '../../app.properties';
import { AbstractService } from '../abstract-service';
import { CurrencyService } from './currency.service';
import { Currency } from '../../models/Currency';

/**
 * App Properties service
 * 
 * @export
 * @class AppPropertiesService
 * @extends {AbstractService}
 */
@Injectable()
export class AppPropertiesService extends AbstractService {
    public defaultCurrency: Currency;
    public currencyObservable: Observable<Currency>;

    /**
     * Creates an instance of AppPropertiesService.
     * @param {AppProperties} appProps 
     * 
     * @memberOf AppPropertiesService
     */
    constructor(
        @Inject(AppProperties) 
        protected appProps: AppProperties,
        protected currencyService: CurrencyService) {
        super(appProps);
    }
    
    public getProperties(): AppProperties {
        return this.appProps;
    }

    /**
     * Returns a observable for currency values
     * 
     * @returns {Observable<Currency>} 
     * 
     * @memberOf AppPropertiesService
     */
    public getDefaultCurrency(): Observable<Currency> {
        if (this.defaultCurrency) {
            return Observable.of(this.defaultCurrency.clone(Currency));
        }
        
        // @TODO Retrieve these values from the web service for the currently active organization
        let uid: number = this.appProps.get(AppProperties.DEFAULT_CURRENCY_KEY);
        let isoCode: string = this.appProps.get(AppProperties.DEFAULT_CURRENCY_CODE_KEY);
        
        this.currencyObservable = Observable.create((observer: Observer<Currency>) => {
            if (! this.appProps.get(AppProperties.DEFAULT_CURRENCY_KEY)) {
                return this.currencyService.getById(uid).subscribe(currency => {
                    if (currency) {
                        this.defaultCurrency = new Currency(currency);
                        observer.next(this.defaultCurrency.clone(Currency));
                        observer.complete();
                    } else {
                        observer.error(`We were unable to find a currency with this UID "${uid}"`);
                    }
                }, error => {
                    observer.error(error);
                });
            } else {
                return this.currencyService.getByIsoCode(isoCode).subscribe(currencies => {
                    if (currencies && currencies.length == 1) {
                        this.defaultCurrency = new Currency(currencies[0]);
                        observer.next(this.defaultCurrency.clone(Currency));
                        observer.complete();
                    } else {
                        observer.error(`We were unable to find a currency with ISO code "${isoCode}"`);
                    }
                }, error => {
                    observer.error(error);
                });
            }
        });
        return this.currencyObservable;
    }
}





