import { Port } from './Port';
import { Party } from './Party';
import { CargoItem } from './CargoItem';
import { VesselCall } from './VesselCall';
import { Currency } from './Currency';
import { CargoTypeAware } from './CargoTypeAware';
import { CommodityType } from './CommodityType';
import { ChargeProfileGroup } from './ChargeProfileGroup';
import { ShipmentSpecificCharge } from './ShipmentSpecificCharge';

export class Shipment extends CargoTypeAware {
    static SHIPMENT_STATUS: Array<{[key: string]: string}> = [
        { value: 'Initial', name: 'Initial'},
        { value: 'Validated', name: 'Validated'},
        { value: 'Approved', name: 'Approved' },
        { value: 'Released', name: 'Released' },
        { value: 'Held', name: 'Held' }
    ];
    static TRANSIT_TYPES: Array<{[key: string]: string}> = [
        { value: 'Domestic', name: 'Domestic' }, 
        { value: 'Transshipment', name: 'Transshipment' }
    ];
    static DOCUMENT_TYPES: Array<{[key: string]: string}> = [
        { value: 'Import', name: 'Import'}, 
        { value: 'Export', name: 'Export'}
    ];
    static CONSOLIDATION_TYPES: Array<{[key: string]: string}> = [
        { value: 'Other', name: 'Other' },
        { value: 'FCL', name: 'FCL' },
        { value: 'LCL', name: 'LCL' }
    ];
    	
    /**
     * Statuses: Initial, Validated, Approved, Released, Held
     */
    public shipmentStatus: 'Initial' | 'Validated' | 'Approved' | 'Released' | 'Held' = 'Initial';
    /**
     * Types: Domestic, Transshipment
     */
    public transitType: 'Domestic' | 'Transshipment';
    /**
     * Types: Import, Export
     */
    public documentType: 'Import' | 'Export';
    /**
     * Types: Other, FCL, LCL
     */
    public consolidationType: 'Other' | 'FCL' | 'LCL';
    
    public shipmentNumber: string;
    
    public item: string;
    
    public masterShipment: Shipment | number;
    public masterShipmentId: number;
    
    public vesselCall: VesselCall | number;
    public vesselCallId: number;
    
    public commodityType: CommodityType | number;
    public commodityTypeId: number;
    
    public shippingLine: Party | number;
    public shippingLineId: number;
    
    public consignee: Party | number;
    public consigneeId: number;
    
    public shipper: Party | number;
    public shipperId: number;
    
    public agent: Party | number;
    public agentId: number;
    
    public notifyParty: Party | number;
    public notifyPartyId: number;
    
    public freightForwarder: Party | number;
    public freightForwarderId: Party | number;
    
    public portOfOrigin: Port | number;
    public portOfOriginId: Port | number;
    
    public portOfDestination: Port | number;
    public portOfDestinationId: number;
    
    public portOfLoading: Port | number;
    public portOfLoadingId: number;
    
    public portOfDischarge: Port | number;
    public portOfDischargeId: number;
    
    public currency: Currency | number;
    public currencyId: number;
    
    public chargeProfileGroup: ChargeProfileGroup | number;
    public chargeProfileGroupId: number;
    
    public cargoItems: Array<CargoItem> = new Array();

	public shipmentSpecificCharges: Array <ShipmentSpecificCharge> = new Array();
	
    public documentIndicator: string;
    public houseShipments: Array<Shipment> = new Array();

    public held: boolean;
    public approved: boolean;

    public arrivalNoticeSent: boolean;

    public validated: boolean;

    public hasComments: boolean;

    public presentationStatus: string;

    constructor(data?: any) {
        super(data);
        
        if (data) {
			this.shipmentStatus = data.shipmentStatus;
            this.transitType = data.transitType;
            this.documentType = data.documentType;
            this.consolidationType = data.consolidationType;
            this.shipmentNumber = data.shipmentNumber;
            this.item = data.item;
            
            if (data!.masterShipment) {
                this.masterShipment = new Shipment(data.masterShipment);
                this.masterShipmentId = this.masterShipment.uid;
            }
            if (data!.vesselCall) {
                this.vesselCall = new VesselCall(data.vesselCall);
                this.vesselCallId = this.vesselCall.uid;
            }
            if (data!.commodityType) {
                this.commodityType = new CommodityType(data.commodityType);
                this.commodityTypeId = this.commodityType.uid;
            }
            if (data!.shippingLine) {
                this.shippingLine = new Party(data.shippingLine);
                this.shippingLineId = this.shippingLine.uid;
            }
            if (data!.consignee) {
                this.consignee = new Party(data.consignee);
                this.consigneeId = this.consignee.uid;
            }
            if (data!.shipper) {
                this.shipper = new Party(data.shipper);
                this.shipperId = this.shipper.uid;
            }
            if (data!.agent) {
                this.agent = new Party(data.agent);
                this.agentId = this.agent.uid;
            }
            if (data!.notifyParty) {
                this.notifyParty = new Party(data.notifyParty);
                this.notifyPartyId = this.notifyParty.uid;
            }
            if (data!.freightForwarder) {
                this.freightForwarder = new Party(data.freightForwarder);
                this.freightForwarderId = this.freightForwarder.uid;
            }
            if (data!.portOfOrigin) {
                this.portOfOrigin = new Port(data.portOfOrigin);
                this.portOfOriginId = this.portOfOrigin.uid;
            }
            if (data!.portOfDestination) {
                this.portOfDestination = new Port(data.portOfDestination);
                this.portOfDestinationId = this.portOfDestination.uid;
            }
            if (data!.portOfLoading) {
                this.portOfLoading = new Port(data.portOfLoading);
                this.portOfLoadingId = this.portOfLoading.uid;
            }
            if (data!.portOfDischarge) {
                this.portOfDischarge = new Port(data.portOfDischarge);
                this.portOfDischargeId = this.portOfDischarge.uid;
            }
            if (data!.currency) {
                this.currency = new Currency(data.currency);
                this.currencyId = this.currency.uid;
            }
            if (data!.chargeProfileGroup) {
                this.chargeProfileGroup = new ChargeProfileGroup(data.chargeProfileGroup);
                this.chargeProfileGroupId = this.chargeProfileGroup.uid;
            }
            
            if (data.cargoItems && data.cargoItems.length) {
                data.cargoItems.forEach(cargoItem => {
                    this.cargoItems.push(new CargoItem(cargoItem));
                });
            }

            if (data.shipmentSpecificCharges && data.shipmentSpecificCharges.length) {
                data.shipmentSpecificCharges.forEach(shipmentSpecificCharge => {
                    this.shipmentSpecificCharges.push(new ShipmentSpecificCharge(shipmentSpecificCharge));
                });
            }			
			
            this.documentIndicator = data.documentIndicator;
            if(data.houseShipments){
                this.houseShipments = data.houseShipments;
            }
            this.held = data.held;
            this.approved = data.approved;

            this.arrivalNoticeSent = data.arrivalNoticeSent;
            this.validated = data.validated;

            this.hasComments = data.hasComments;

            this.presentationStatus = data.presentationStatus;
        }
    }
    
    public prepareData() {
        if (this!.masterShipmentId) {
            this.masterShipment = this.masterShipmentId;
        }
        if (this!.vesselCallId) {
            this.vesselCall = this.vesselCallId;
        }
        if (this!.commodityTypeId) {
            this.commodityType = this.commodityTypeId;
        }
        if (this!.shippingLineId) {
            this.shippingLine = this.shippingLineId;
        }
        if (this!.consigneeId) {
            this.consignee = this.consigneeId;
        }
        if (this!.shipperId) {
            this.shipper = this.shipperId;
        }
        if (this!.agentId) {
            this.agent = this.agentId;
        }
        if (this!.notifyPartyId) {
            this.notifyParty = this.notifyPartyId;
        }
        if (this!.freightForwarderId) {
            this.freightForwarder = this.freightForwarderId;
        }
        if (this!.portOfOriginId) {
            this.portOfOrigin = this.portOfOriginId;
        }
        if (this!.portOfDestinationId) {
            this.portOfDestination = this.portOfDestinationId;
        }
        if (this!.portOfLoadingId) {
            this.portOfLoading = this.portOfLoadingId;
        }
        if (this!.portOfDischargeId) {
            this.portOfDischarge = this.portOfDischargeId;
        }
        if (this!.currencyId) {
            this.currency = this.currencyId;
        }
        if (this!.chargeProfileGroupId) {
            this.chargeProfileGroup = this.chargeProfileGroupId;
        }
    }
}



