import { Pipe, PipeTransform } from '@angular/core';
import { EnumBasePipe } from './enum-base.pipe'
import { Invoice } from '../../models/Invoice'

@Pipe({
  name: 'invoicestatus'
})
export class InvoiceStatusPipe extends EnumBasePipe implements PipeTransform {

    constructor() {
        super();
        this.values = Invoice.INVOICE_STATUS
    }

}



