import {Injectable, Inject} from "@angular/core";
import {AbstractService} from "../abstract-service";
import {Http, Response} from "@angular/http";
import {AppProperties} from "../../app.properties";
import {Observable} from "rxjs";

@Injectable()
export class ChangePasswordService extends AbstractService{

    constructor(private http: Http, @Inject(AppProperties) appProps: AppProperties) {
        super(appProps);
    }

    public changePassword(username: string, oldPassword: string, newPassword: string) : Observable<string> {
        return this.http.put(this.apiBaseUrl + '/auth/login/password?username=' + username, {oldPassword: oldPassword, newPassword: newPassword}, this.getAnonymousRequestOptions())
            .catch(this.handleError);
    }

    public handleError(error: Response) {
            return Observable.throw(error);
    }
}



