import { VesselType } from './VesselType';
import { ShippingLine } from './ShippingLine';
import { DomainObject } from './DomainObject';

export class Vessel extends DomainObject {
    public uid: number;
    public vesselCode: string;
    public vesselName: string;
    public vesselNumber: string;
    public callSign: string;
    public vesselType: any;
    public vesselOperator: ShippingLine;
    public vesselNationality: any;
	public cityOfRegistration: string;
	public registrationNumber: string;
	public vesselLength: number;
	public maxGrossRegisteredTonnage: string;
	public maxNetRegisteredTonnage: string;
	public maximumBeam: number;
	public maximumDraught: number;
	public netRevenueTonnage: string;
        
    constructor(data?: any) {
        super(data);
        
        if (data) {
            this.vesselCode = data.vesselCode;
			this.vesselName = data.vesselName;
            this.vesselNumber = data.vesselNumber;
            this.callSign = data.callSign;
			this.vesselType = new VesselType(data.vesselType);            
            this.vesselOperator = new ShippingLine(data.vesselOperator);
			this.vesselNationality = data.vesselNationality;
			this.cityOfRegistration = data.cityOfRegistration;
			this.registrationNumber = data.registrationNumber;
			this.vesselLength = data.vesselLength;
			this.maxGrossRegisteredTonnage = data.maxGrossRegisteredTonnage;
			this.maxNetRegisteredTonnage = data.maxNetRegisteredTonnage;
			this.maximumBeam = data.maximumBeam;
			this.maximumDraught = data.maximumDraught;
			this.netRevenueTonnage = data.netRevenueTonnage;
			}
    }
    
    public prepareData(): void {}
}


