import { DomainObject } from './DomainObject';
import { Party } from './Party';
import { AccountWithdrawal } from './AccountWithdrawal';

export class DepositAccount extends DomainObject {
    public accountHolder: number | Party;
    public deposits: any;
    public withdrawals: Array<AccountWithdrawal> = [];
        
    constructor(data?: any) {
        super(data);
        
        if (data) {
            this.accountHolder = new Party(data.accountHolder);
            if (data.deposits && data.deposits.length > 0) {
                data.deposits.forEach((withdrawal) => {
                    this.withdrawals.push(new AccountWithdrawal(withdrawal));
                });
            }
        }
    }
    
    public prepareData(): void {
    }
}


