import { DomainObject } from './DomainObject';
import { ChargeProfile } from './ChargeProfile';

export class ChargeProfileGroup extends DomainObject {
    public name: string;
    public chargeProfiles: Array<ChargeProfile> = new Array();
    
    constructor(data?:any) {
        super(data);
        
        if (data) {
            this.name = data.name;
                        
            if (data.chargeProfiles) {
                data.chargeProfiles.forEach((item) => {
                    this.chargeProfiles.push(new ChargeProfile(item));
                });
            }
        }
    }
    
    public prepareData() {
        
    }
}


