import { DomainObject } from './DomainObject';
import { MeasurementInformation } from './MeasurementInformation';
import { PackagingInformation } from './PackagingInformation';
import { CommodityType } from './CommodityType';
import { Container } from './Container';
import { MotorVehicle } from './MotorVehicle';
import { Shipment } from './Shipment';
import { HarmonizedSystemCode } from './HarmonizedSystemCode';

export class CargoItem extends DomainObject {
    static TALLY_STATUS: Array<{[key: string]: string}> = [
        { value: 'Tallied', name: 'Pre-Tallied'},
        { value: 'NotTallied', name: 'Not Tallied'}
    ];
    public itemNumber: number; // Should be pre-populated
    public quantity: number;
    public tallyStatus: "Tallied" | "NotTallied";
    public description: string;
    public commodityType: CommodityType | string | number;
    public commodityTypeId: number;
    public harmonizedSystemCode: HarmonizedSystemCode | string | number;
    public harmonizedSystemCodeId: number;
    public shipment: Shipment | number;
    public shipmentId: number;
    
    public packagingInformation: PackagingInformation = new PackagingInformation();
    public measurementInformation: MeasurementInformation = new MeasurementInformation();
    public containers: Array<Container> = [];
    public motorVehicles: Array<MotorVehicle> = [];
    
    // @todo: REMOVE
    public inventoryQuantity: number;
        
    constructor(data?: any) {
        super(data);
        
        if (data) {
            this.itemNumber = data.itemNumber;
            this.quantity = data.quantity;
            this.tallyStatus = data.tallyStatus ? data.tallyStatus : "NotTallied";
            this.description = data.description;
            this.shipment = new Shipment(data.shipment);
            this.shipmentId = this.shipment.uid;
            
            if (data.commodityType) {
                this.commodityType = new CommodityType(data.commodityType);
                this.commodityTypeId = this.commodityType.uid;
            } else {
                this.commodityType = this.commodityTypeId = null;
            }
            
            if (data.harmonizedSystemCode) {
                this.harmonizedSystemCode = new HarmonizedSystemCode(data.harmonizedSystemCode);
                this.harmonizedSystemCodeId = this.harmonizedSystemCode.uid;
            } else {
                this.harmonizedSystemCode = this.harmonizedSystemCodeId = null;
            }
            
            if (data.containers instanceof Array) {
                data.containers.forEach(container => {
                    this.containers.push(new Container(container));
                });
            }
            
            if (data.motorVehicles instanceof Array) {
                data.motorVehicles.forEach(vehicle => {
                    this.motorVehicles.push(new MotorVehicle(vehicle));
                });
            }
            
            if (data.packagingInformation) {
                this.packagingInformation = new PackagingInformation(data.packagingInformation);
            }
            
            if (data.measurementInformation) {
                this.measurementInformation = new MeasurementInformation(data.measurementInformation);
            }
            
            // @todo: Remove. Inventory should be retrieved from a webservice
            this.inventoryQuantity = data.inventoryQuantity;
        } else {
            this.tallyStatus = "NotTallied";
            this.quantity = 0;
            this.harmonizedSystemCode = this.harmonizedSystemCodeId = null;
            this.commodityType = this.commodityTypeId = null;
            this.packagingInformation.packageType = this.packagingInformation.packageTypeId = null;
        }
    }
    
    public prepareData() {
        if (this.commodityType instanceof CommodityType) {
            this.commodityType = this.commodityType.uid;
        } else {
            this.commodityType = this.commodityTypeId;
        }
        
        if (this.harmonizedSystemCode instanceof HarmonizedSystemCode) {
            this.harmonizedSystemCode = this.harmonizedSystemCode.uid;
        } else {
            this.harmonizedSystemCode = this.harmonizedSystemCodeId;
        }
        
        if (this.shipment instanceof Shipment) {
            this.shipment = this.shipment.uid;
        } else {
            this.shipment = this.shipmentId;
        }
        
        this.packagingInformation.prepareData();
        this.measurementInformation.prepareData();
    }
}


