import { Component } from '@angular/core';
import { OnInit } from '@angular/core';
import { BaseLookupDialogComponent } from "./base-lookup-dialog.component";
import { VesselCallService } from "../../../services/vessel-call/vessel-call.service";

@Component({
    selector: 'vessel-call-lookup-dialog',
    template: require('./vessel-call-lookup-dialog.component.html'),
    providers: [
        VesselCallService
    ]
})
export class VesselCallLookupDialogComponent extends BaseLookupDialogComponent implements OnInit {

    constructor(private vesselCallService: VesselCallService) {
        super();
    }

    ngOnInit() {
        this.vesselCallService.getAll().subscribe((consignees) => {
            this.tableData = consignees;            
        });
    }
    
    protected doFilter(filter: string) {
        this.vesselCallService.getByFilter(filter).subscribe((consignees) => {
            this.tableData = consignees;
            
            if (this.filterQueue.length > 0) {
                this.doFilter(this.filterQueue.pop());
            }
        });
    }
}



